/*---------------------------------------------*/
/*	SECTION DOUBLE DIAGONAL
/*---------------------------------------------*/
.section-double-diagonal{
	position: relative;
	padding-top: $sep-double-diagonal-spacer-top;
	z-index: 1;
	

	&::before, &::after{
		left: 0;
		top: -($sep-double-diagonal-top);
		z-index: -1;
		width: 100%;
		height: $sep-double-diagonal-height;
		background: inherit;
		transform: skewY(-($sep-double-diagonal-front-angle));
	}//before / after

	&::before{
		height: 50%;
		background: #95a5a6;
		transform: skewY(-($sep-double-diagonal-back-angle));
		transform-origin: $sep-double-diagonal-back-angle-origin 0;
	}//before



	// LEFT
	&.left{

		&::after{
			transform: skewY($sep-double-diagonal-front-angle);
		}//after

		&::before{
			transform: skewY($sep-double-diagonal-back-angle);
			transform-origin: $sep-double-diagonal-back-angle-reverse-origin 0;
		}//before

	}//left




	// BOTTOM
	&.bottom{

		&::before,
		&::after{
			top: auto;
			bottom: -($sep-double-diagonal-top);
		}

		&::after{
			transform: skewY($sep-double-diagonal-front-angle);
		}//after

		&::before{
			transform: skewY($sep-double-diagonal-back-angle);
			transform-origin: $sep-double-diagonal-back-angle-origin 0;
		}//before

	}//bottom




	// LEFT BOTTOM
	&.left.bottom{

		&::before,
		&::after{
			top: auto;
			bottom: -($sep-double-diagonal-top);
		}

		&::after{
			transform: skewY(-($sep-double-diagonal-front-angle));
		}//after

		&::before{
			transform: skewY(-($sep-double-diagonal-back-angle));
			transform-origin: $sep-double-diagonal-back-angle-reverse-origin 0;
		}//before

	}//left / bottom

}//section-double-diagonal




