/*---------------------------------------------------------*/
/*	BUTTONS
/*---------------------------------------------------------*/
@import 'buttons/btn-mixins';



// Default styles for buttons "without any class"
button{
	@include btn-style(
		$background: 		#fff, 
		$border: 				$gray-light, 
		$color: 				$gray, 
		$bgHover: 			$gray-lighter, 
		$borderHover: 	darken($gray-light, 10%), 
		$colorHover: 		darken($gray, 10%)
	);

	padding: 				$btn-spacer-y $btn-spacer-x; 
}//button




.btn{
	background: 				$btn-background;
	font-size: 					$btn-font-size; 
	font-weight: 				$btn-font-weight; 
	// letter-spacing: 		.5px;
	padding: 						$btn-spacer-y $btn-spacer-x; 
	display:						inline-flex;
	justify-content: 		space-evenly;
	align-items: 				center;
	transition: 				$btn-transition;
	border-radius: 			$global-radius;


	@include linkHover(text-decoration none);


	&.btn-default, &.btn-secondary	{ @include btn-style($gray-lighter, $gray-lighter, $gray); }
	&.btn-primary	{ @include btn-style($brand-primary, $brand-primary, #fff, $brand-primary-dark, $brand-primary-dark); }
	&.btn-success	{ @include btn-style($brand-success, $brand-success, #fff, $brand-success-dark, $brand-success-dark); }
	&.btn-info 		{ @include btn-style($brand-info, $brand-info, #fff, $brand-info-dark, $brand-info-dark); }
	&.btn-warning	{ @include btn-style($brand-warning, $brand-warning, #fff, $brand-warning-dark, $brand-warning-dark); }
	&.btn-danger	{ @include btn-style($brand-danger, $brand-danger, #fff, $brand-danger-dark, $brand-danger-dark); }
	&.btn-light		{ @include btn-style(#fff, #fff, #444, $colorHover:$brand-primary); }
	&.btn-dark		{ @include btn-style($gray-darker, $gray-darker, #fff, #000, #000, #fff); }
	&.btn-link		{ @include btn-style(transparent, transparent, #444, $colorHover:$brand-primary); }
	




	// BUTTON OUTLINE
	&.btn-outline 									{ @include btn-outline-color(#fff); }
	&.btn-outline.btn-default, 
	&.btn-outline.btn-secondary, 
	&.btn-outline-secondary 				{ @include btn-outline-color($gray-lighter); }
	&.btn-outline.btn-primary, 
	&.btn-outline-primary 					{ @include btn-outline-color($brand-primary); }
	&.btn-outline.btn-success, 
	&.btn-outline-success 					{ @include btn-outline-color($brand-success); }
	&.btn-outline.btn-info, 
	&.btn-outline-info 							{ @include btn-outline-color($brand-info); }
	&.btn-outline.btn-warning, 
	&.btn-outline-warning 					{ @include btn-outline-color($brand-warning); }
	&.btn-outline.btn-danger, 
	&.btn-outline-danger 						{ @include btn-outline-color($brand-danger); }


	// BUTTON INVERSE
	&.btn-inverse.btn-default, 
	&.btn-inverse.btn-secondary 		{	@include btn-style(#444, #444, #eee, #eee, #eee, #444); }
	&.btn-inverse.btn-primary 			{ @include btn-style(#fff, $brand-primary, $brand-primary, $brand-primary, $brand-primary, #fff); }
	&.btn-inverse.btn-success 			{ @include btn-style(#fff, $brand-success, $brand-success, $brand-success, $brand-success, #fff); }
	&.btn-inverse.btn-info 					{ @include btn-style(#fff, $brand-info, $brand-info, $brand-info, $brand-info, #fff); }
	&.btn-inverse.btn-warning 			{ @include btn-style(#fff, $brand-warning, $brand-warning, $brand-warning, $brand-warning, #fff); }
	&.btn-inverse.btn-danger 				{ @include btn-style(#fff, $brand-danger, $brand-danger, $brand-danger, $brand-danger, #fff); }


	// BUTTON ROUNDED
	&.btn-rounded{ border-radius: 1000px; }


	// BUTTON SHARP
	&.btn-sharp{ border-radius: 0 !important; }


}//btn




/*-------------------------------------------------*/
/*	BUTTONS CONS
/*	Cons provide a fixed width for buttons
/*-------------------------------------------------*/
.btn-cons{
	min-width: $btn-cons-width; 
}//btn-cons







/*-------------------------------------------------*/
/*  BUTTON SIZES
/*-------------------------------------------------*/
.btn-lg{
	font-size: 					$btn-lg-font-size; 
	padding: 						$btn-lg-padding-y $btn-lg-padding-x;
}

.btn-sm{
	font-size: 					$btn-sm-font-size; 
	font-weight: 				600;
	padding: 						$btn-sm-padding-y $btn-sm-padding-x;
}

.btn-xs{
	font-size: 					$btn-xs-font-size; 
	font-weight: 				600;
	padding: 						$btn-xs-padding-y $btn-xs-padding-x;
}





/*-------------------------------------------------*/
/*  BUTTONS CLOSE
/*-------------------------------------------------*/
button.close{
	font-size: 					$btn-close-font-size;
	line-height: 				.5;
	opacity: 						.6;
	text-shadow: 				none;
	box-shadow: 				none;


	&:hover, &:active, &:focus{
		background: 			transparent;
		border-color: 		transparent;
		opacity: 					1;
	}//::hover
}//close





@import 'buttons/btn-icons';
@import 'buttons/btn-dropdown';