/*---------------------------------------------------------*/
/*	GLOBALS
/*---------------------------------------------------------*/
.no-decoration 	{text-decoration: none !important;}


b, strong, .bold{
	font-weight: $font-bold;
}


.italic{
	font-style: italic;
}


.lead{
	font-size: $lead-fz;
	margin: 0;
}//lead

small{
	font-size: $font-small-size;
}


sup, sub{
	font-size: $supsub-font-size;
	line-height: 0;
}//sup / sub


sup{
	vertical-align: super;
	top: .1rem;
}//sup


sub{
	vertical-align: sub;
}//sub


p{
	margin-bottom: $p-spacer;

	&:last-child{
		margin-bottom: 0;
	}
}






/*---------------------------------------------------------*/
/*	HEADS
/*---------------------------------------------------------*/
h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6{
	margin-top: 0;

	&.font-secondary{
		font-weight: 600;
	}//font-secondary

	small{
		color: inherit;
	}//small

}//h



h1, .h1{
	font-size: $font-h1-font-size;
	font-weight: $font-h1-font-weight;
}

h2, .h2{
	font-size: $font-h2-font-size;
	font-weight: $font-h2-font-weight;
}

h3, .h3{
	font-size: $font-h3-font-size;
	font-weight: $font-h3-font-weight;
}

h4, .h4{
	font-size: $font-h4-font-size;
	font-weight: $font-h4-font-weight;
}

h5, .h5{
	font-size: $font-h5-font-size;
	font-weight: $font-h5-font-weight;
}

h6, .h6{
	font-size: $font-h6-font-size;
	font-weight: $font-h6-font-weight;
}






/*---------------------------------------------------------*/
/*	DISPLAY HEADINGS
/*---------------------------------------------------------*/
.display-1{
	font-size: $display-1-fz;
	font-weight: $display-fw;
	line-height: $display-lh;
}//display-1


.display-2{
	font-size: $display-2-fz;
	font-weight: $display-fw;
	line-height: $display-lh;
}//display-2


.display-3{
	font-size: $display-3-fz;
	font-weight: $display-fw;
	line-height: $display-lh;
}//display-3


.display-4{
	font-size: $display-4-fz;
	font-weight: $display-fw;
	line-height: $display-lh;
}//display-4






/*---------------------------------------------------------*/
/*	FONT SIZE
/*---------------------------------------------------------*/
.fz50,
.fz-50{font-size:50% !important;}

.fz65,
.fz-65{font-size:65% !important;}

.fz80,
.fz-80{font-size:80% !important;}

.fz90,
.fz-90{font-size:90% !important;}




@for $i from 3 through 20{
	.fz#{50*$i},
	.fz-#{50*$i}{font-size:#{50*$i}% !important;}
	.fz#{50*$i} [class*="fa"]:before, 
	.fz#{50*$i} [class*="fa"]:after,
	.fz#{50*$i} [class*="ion-"]:before, 
	.fz#{50*$i} [class*="ion-"]:after,
	.fz#{50*$i} [class*="icon-"]:before, 
	.fz#{50*$i} [class*="icon-"]:after{
		font-size:#{50*$i}% !important;
	}
}





/*---------------------------------------------------------*/
/*	FONT WEIGHT
/*---------------------------------------------------------*/
.fw-100{font-weight:100 !important;}
.fw-200{font-weight:200 !important;}
.fw-300{font-weight:300 !important;}
.fw-400{font-weight:400 !important;}
.fw-500{font-weight:500 !important;}
.fw-600{font-weight:600 !important;}
.fw-700{font-weight:700 !important;}
.fw-800{font-weight:800 !important;}








/*---------------------------------------------------------*/
/*	LISTS
/*---------------------------------------------------------*/
ul, ol{
	
	li{
		line-height: $list-line-height;
	}

}//ul, ol


ol{
	padding-left: $list-ol-spacer-x;
	counter-reset: item;

	li{
		counter-increment: item;
	}

	ol{
		padding-left: 0;
	}

	ol li{
		display: table;
	}

	ol li::before{
		content: counters(item, ".") " "; 
		counter-increment: item;
	}
}//ol




.list-dot{
	padding-left: $list-dot-spacer-x;

	li{
		list-style: none;

		&::before{
			content: '\b7';
			font-weight: 600;
			padding-right: $list-dot-spacer-x/1.4;
			margin-left: -($list-dot-spacer-x - 1);
			position: relative;
		}//before

		ul{
			padding-left: $list-dot-spacer-x;
		}//ul

	}//li

}//list-dot




.list-dash{
	padding-left: 13px;

	li{
		list-style: none;

		&::before{
			content: '–';
			padding-right: 9px;
			margin-left: -12px;
			position: relative;
		}//before

		ul{
			padding-left: 13px;
		}//ul

	}//li

}//list-dash






// Iconized lists
.fa-ul .fa-li{
	font-size: 130%;
}//fa-li





// DESCRIPTION LISTS
dl{

	dt{}

	dd{
		margin-bottom: $dd-margin;
	}

}//dl







/*---------------------------------------------------------*/
/*	ICONS
/*---------------------------------------------------------*/
.icon-lg{
	font-size: 1.6rem !important;
}

.icon-lg-2{
	font-size: 3rem !important;
}

.icon-lg-3{
	font-size: 4rem !important;
}

.icon-lg-4{
	font-size: 5rem !important;
}







/*---------------------------------------------------------*/
/*	ADDITIONALS
/*---------------------------------------------------------*/
a, 
:active, 
:focus{ 
	outline: 0 none !important; 
	outline-offset: 0 !important;
}


a{
	transition: .2s ease-out;
}//a


hr{
	border-color: $hr-border-color;
}//hr


.text-muted{ 
	color: $text-muted-color !important;
	font-weight: $text-muted-font-weight;
}//text-muted






/*---------------------------------------------------------*/
/*	LETTER SPACING
/*---------------------------------------------------------*/
.ls-1{ letter-spacing: .15rem; }
.ls-2{ letter-spacing: .25rem; }
.ls-3{ letter-spacing: .50rem; }
.ls-4{ letter-spacing: .75rem; }
.ls-5{ letter-spacing: 1rem; }
.ls-6{ letter-spacing: 1.25rem; }
.ls-7{ letter-spacing: 1.50rem; }
.ls-8{ letter-spacing: 1.75rem; }
.ls-9{ letter-spacing: 2rem; }






/*---------------------------------------------------------*/
/*	CSS3 COLUMNS
/*---------------------------------------------------------*/
@for $i from 2 through 8{
	.columns-#{$i}{ 
		columns: #{$i}; 
		column-gap: 30px;
	}
}


@media screen and (max-width: $screen-xs) {
	@for $i from 2 through 8{
		.columns-#{$i}{ 
			columns: 1; 
			column-gap: 30px;
		}
	}
}


@media screen and (min-width: $screen-sm) {
	@for $i from 2 through 8{
		.columns-sm-#{$i}{ 
			columns: #{$i}; 
			column-gap: 30px;
		}
	}
}


@media screen and (min-width: $screen-md) {
	@for $i from 2 through 8{
		.columns-md-#{$i}{ 
			columns: #{$i}; 
			column-gap: 30px;
		}
	}
}


@media screen and (min-width: $screen-lg) {
	@for $i from 2 through 8{
		.columns-lg-#{$i}{ 
			columns: #{$i}; 
			column-gap: 30px;
		}
	}
}