/*---------------------------------------------*/
/*	SECTION DIAGONAL
/*---------------------------------------------*/
.section-diagonal{
	position: relative;
	padding-top: $sep-diagonal-spacer-top;
	z-index: 1;
	

	&::before{
		background: inherit;
		left: 0;
		top: -($sep-diagonal-top);
		width: 100%;
		height: $sep-diagonal-height;
		transform: skewY(-($sep-diagonal-angle));
		z-index: -1;
	}//before


	// LEFT
	&.left{

		&::before{
			transform: skewY($sep-diagonal-angle);
		}//before

	}//left



	// BOTTOM
	&.bottom{

		&::before{
			top: auto;
			bottom: -($sep-diagonal-top);
			transform: skewY($sep-diagonal-angle);
		}//before

	}//bottom



	// LEFT BOTTOM
	&.left.bottom{

		&::before{
			top: auto;
			bottom: -($sep-diagonal-top);
			transform: skewY(-($sep-diagonal-angle));
		}//before

	}//left / bottom

}//section-simple-diagonal




