/*-------------------------------------------------*/
/*  SIMPLE LIGHTBOX  */
/*-------------------------------------------------*/
.simple-lightbox.sl-wrapper{

	.sl-info{
		background: $sl-info-bg-color; 
		color: $sl-info-color; 
		font-size: $sl-info-font-size; 
		text-align: center; 
		width: 100%; 
		height: 100%; 
		display: flex; 
		justify-content: center; 
		position: absolute;

		.btn{
			border-width: 1px; 
			border-style: solid; 
			font-size: .8rem; 
			padding: 5px 15px;
		}//btn

	}//sl-info



	.sl-navigation{

		button{
			background-repeat: no-repeat; 
			background-position: center;
			font-size: 0 !important; 
			height: $sl-btn-height !important;
			width: $sl-btn-width !important; 
			margin-top: $sl-btn-margin-top !important; 
			opacity: $sl-btn-opacity;
			
			@include linkHover(opacity $sl-btn-hover-opacity);



			&.sl-prev{
				background-image: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' viewBox='-1 0 13 24'><path d='M11 1l-9 10.56 9 11.44' stroke='#{$sl-btn-stroke}' fill='#{$sl-btn-fill}' stroke-width='#{$sl-btn-stroke-width}' stroke-linecap='#{$sl-btn-stroke-linecap}'/></svg>");

				// background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="9 3 13 26"><polyline points="20 27 11 15.9447962 20 5" style="stroke: #{$sl-btn-stroke}; fill: #{$sl-btn-fill}; stroke-width: #{$sl-btn-stroke-width}; stroke-linecap: #{$sl-btn-stroke-linecap};"></polyline></svg>');
			}//sl-prev


			&.sl-next{
				background-image: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' viewBox='9 4 13 24'><path d='M11 5 20 15.9447962 11 27 11 27' stroke='#{$sl-btn-stroke}' fill='#{$sl-btn-fill}' stroke-width='#{$sl-btn-stroke-width}' stroke-linecap='#{$sl-btn-stroke-linecap}'/></svg>");
				
				// background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="9 3 13 26"><polyline points="11 5 20 15.9447962 11 27 11 27" style="stroke: #{$sl-btn-stroke}; fill: #{$sl-btn-fill}; stroke-width: #{$sl-btn-stroke-width}; stroke-linecap: #{$sl-btn-stroke-linecap};"></polyline></svg>');
			}//sl-next

		}//button

	}//sl-navigation


	.sl-close{
		background-image: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' viewBox='9.5 6 13 21'><path d='M26,7 L5.93105805,25.0620478' stroke='#{$sl-btn-close-stroke}' fill='#{$sl-btn-close-fill}' stroke-width='#{$sl-btn-close-stroke-width}' stroke-linecap='#{$sl-btn-close-stroke-linecap}'/><path d='M26,25 L5.93105805,6.93795225' stroke='#{$sl-btn-close-stroke}' fill='#{$sl-btn-close-fill}' stroke-width='#{$sl-btn-close-stroke-width}' stroke-linecap='#{$sl-btn-close-stroke-linecap}'/></svg>");


		background-repeat: no-repeat; 
		background-position: center;
		font-size: 0 !important; 
		opacity: $sl-btn-opacity;

		@include linkHover(opacity $sl-btn-hover-opacity);

	}//sl-close

}//sl-wrapper



.simple-lightbox + .sl-overlay{
	background: $sl-overlay-background;
	opacity: 1;
}//sl-overlay


