/*-------------------------------------------------*/
/*  PAGINATION
/*-------------------------------------------------*/
.pagination{

	> li > a, 
	> li > span{
		background:transparent; 
		border-color:transparent; 
		color:#444; 
		font-weight:400;
		margin:0 1px;
		border-radius:0;
	}

	> li > a:hover, 
	> li > span:hover{
		background:#eee;
		border-color:#eee;
		color:#444;
	}


	> li:first-child > a, 
	> li:first-child > span,
	> li:last-child > a, 
	> li:last-child > span{border-radius:0;}


	> li.disabled > a,
	> li.disabled > span{
		background: transparent; 
		border-color: transparent; 
		opacity: .5; 
		cursor: default;

		@include linkHover(
			background transparent,
			border-color transparent,
			opacity .5,
			cursor default
		);

	}// > li.disabled > a / > li.disabled > span



	> li.active > a,
	> li.active > span{
		background: #444; 
		border-color: #444;
		color: #fff;

		@include linkHover(
			background #444,
			border-color #444,
			color #fff
		);
	}// > li.active > a / > li.active > span



}//pagination









/*-------------------------------------------------*/
/*  PAGER
/*-------------------------------------------------*/
.pager{

	.previous > a, 
	.previous > span{}

	.next > a, 
	.next > span{}

	.previous > a, 
	.previous > span,
	.next > a, 
	.next > span{
		background:none; 
		color:#444; 
		border:none; 
		padding:10px 0; 
		border-radius:0;

		@include linkHover(background none);
	}// previous > a, previous > span / next > a / next > span

}//pager 












