/*---------------------------------------------------------*/
/*	NUMBER - Touchspin plugin styles
/*---------------------------------------------------------*/
.input-group.bootstrap-touchspin{


	.input-group-btn{
		flex: 1 0 auto;


		.bootstrap-touchspin-up,
		.bootstrap-touchspin-down{
			background: 		$form-background;
			border: 				solid 1px $form-border-color;
			color: 					$form-color;
			font-size: 			$touchspin-font-size;
			font-weight: 		$touchspin-btn-font-weight;
			width: 					100%;
			height: 				$form-ig-btn-size;
			padding: 				0;
			// margin: 				0 -2px !important;
			transition: 		.2s;

			@include linkHover(background $gray-lighter);

		}// bootstrap-touchspin-up / bootstrap-touchspin-down


		.bootstrap-touchspin-down{
			border-right: none;
			border-top-right-radius: 0;
			border-bottom-right-radius: 0;
		}//bootstrap-touchspin-down
	
		.bootstrap-touchspin-up{
			border-left: none;
			border-top-left-radius: 0;
			border-bottom-left-radius: 0;
		}//bootstrap-touchspin-up

	}//input-group-btn


	.form-control{
		border-left: none;
		border-right: none;
		padding-left: 5px;
		padding-right: 5px;
		flex: 1 0 auto;
	}

// 	.form-control.touchspin:not(.touchspin-default),
// 	.input-group-addon:empty + .form-control.touchspin:not(.touchspin-default){ 
// 		// border-radius: $global-radius; 
// 		border-radius: 0; 
// 	}
	
