/*---------------------------------------------------------*/
/*	TABS
/*---------------------------------------------------------*/
$tab-border-color: 			#ddd 												!default;
$tab-color: 						$global-font-color 					!default;
$tab-font-size: 				.8rem 											!default;
$tab-font-weight: 			400 												!default;
$tab-border-radius: 		$global-radius 							!default;

$tab-hover-bg: 					$gray-light									!default;

$tab-active-bg: 				#fff 											!default;

$tab-nav-border: 				solid 1px $tab-border-color !default;
$tab-content-border: 		solid 1px $tab-border-color !default;
$tab-content-font-size: .85rem 											!default;
