/*---------------------------------------------------------*/
/*	NOTIFICATIONS
/*---------------------------------------------------------*/
// ALERTS
$alert-bg: 							$gray-lighter 								!default;
$alert-border-color: 		$gray-lighter									!default;
$alert-color: 					$gray-dark										!default;
$alert-padding-y: 			10px 													!default;
$alert-padding-x: 			15px 													!default;
$alert-font-size: 			.9rem 												!default;
$alert-font-weight: 		400 													!default;
$alert-line-height: 		1.4 													!default;
$alert-shadow: 					none 													!default;

$alert-image-size: 			70px 													!default;

$alert-close-size: 			20px 													!default;




// POPOVER
$popover-background: 					#fff 									!default;
$popover-font-size: 					.8rem 									!default;
$popover-radius: 							3px 										!default;
$popover-shadow: 							none 										!default;

$popover-title-background: 		transparent 						!default;
$popover-title-font-size: 		$popover-font-size 			!default;
$popover-title-font-weight: 	500 										!default;
$popover-title-border: 				1px solid transparent 	!default;
$popover-title-spacer-y: 			14px 										!default;
$popover-title-spacer-x: 			14px 										!default;

$popover-content-spacer-y: 		9px 										!default;
$popover-content-spacer-x: 		14px 										!default;




// TOOLTIP
$tooltip-background: 		#2B3E51 										!default;
$tooltip-color: 				#fff 												!default;
$tooltip-font-size: 		.65rem 												!default;
$tooltip-font-weight: 	500 													!default;
$tooltip-spacer-y: 			6px 													!default;
$tooltip-spacer-x: 			12px 													!default;




// LABELS & BADGES
$label-font-size: 			.8rem 												!default;
$label-font-weight: 		600 													!default;
$label-letter-spacing: 	.5px 													!default;
$label-padding-y: 			3px 													!default;
$label-padding-x: 			10px 													!default;


$badge-color: 					#fff 												!default;
$badge-font-size: 			75% 													!default;
$badge-font-weight: 		600 													!default;
$badge-padding-y: 			2px 													!default;
$badge-padding-x: 			6px 													!default;

$badge-alert-min-size: 	15px 													!default;





// PROGRESS BARS
$progress-background: 	#ECF0F1 										!default;
$progress-height: 			8px 													!default;
$progress-margin: 			20px 													!default;
$progress-rounded: 			300px 												!default;
$progress-box-shadow: 	none 													!default;

$progress-bar-background: #BDC3C8 									!default;










