/*---------------------------------------------*/
/*	SECTION SEPARATORS
/*---------------------------------------------*/
.section-triangle,
.section-diagonal,
.section-double-diagonal,
.section-angle,
.section-curve{
	
	&::before, &::after{
		content: '';
		display: block;
		position: absolute;
		pointer-events: none;
	}//before / after

}//sections





@import 'separators/triangles';
@import 'separators/diagonal';
@import 'separators/double-diagonal';
@import 'separators/angle';
@import 'separators/curve';






















