/*---------------------------------------------------------*/
/*	TABS
/*---------------------------------------------------------*/
.nav-tabs{

	> li > a{
		color: 				$tab-color;
		font-size: 		$tab-font-size;
		font-weight: 	$tab-font-weight;
		margin-right: 0;

		@include linkHover(
			background $tab-hover-bg,
			border-color $tab-hover-bg $tab-hover-bg $tab-border-color
		);

	}// > li > a



	> li.active > a{
		background-color: $tab-active-bg;
		border-color: $tab-border-color $tab-border-color transparent;

		@include linkHover(
			background-color $tab-active-bg,
			// border-color $tab-border-color $tab-border-color transparent
			border-color $tab-border-color,
			border-bottom-color transparent
		);
	}// > li.active > a

}//nav-tabs



// TAB CONTENT
.tab-content{
	font-size: $tab-content-font-size;


	// .fade{
	// 	height: 0;

	// 	&.in{
	// 		height: auto;
	// 	}//in
	// }//fade

}//tab-content






/*---------------------------------------------------------*/
/*	BOTTOM NAV TABS
/*---------------------------------------------------------*/
.tab-content + .nav-tabs{
	border-bottom: none;
	border-top: $tab-content-border;


	> li{
		margin-top: -1px; 
		margin-bottom: 0;
	}//li


	> li > a{
		border-radius: 0 0 $tab-border-radius $tab-border-radius;

		@include linkHover(
			border-color $tab-border-color $tab-hover-bg $tab-hover-bg
		);
	}// > li > a

	

	> li.active > a{
		border-color: transparent $tab-border-color $tab-border-color;

		@include linkHover(
			border-color transparent $tab-border-color $tab-border-color
		);
	}// > li.active > a

}//nav-tabs








/*---------------------------------------------------------*/
/*	SIDEBAR NAV TABS
/*---------------------------------------------------------*/
[class*="col-"].tab-content{
	padding-left: 0; 
	padding-right: 0;
	border-left: $tab-nav-border;
	position: relative;
	z-index: 10;
}//tab-content


[class*="col-"].nav-tabs{
	border: none;
	// border-right: $tab-nav-border;
	padding-left: 0; 
	padding-right: 0;
	position: relative;
	z-index: 11;


	> li{
		margin: 0 -1px 0 0;
	}

	> li > a{
		border-radius: $tab-border-radius 0 0 $tab-border-radius;

		@include linkHover(
			border-color $tab-hover-bg $tab-border-color $tab-hover-bg $tab-hover-bg
		);
	}// > li > a


	> li.active > a, 
	> li.active > a:focus, 
	> li.active > a:hover{
		border-color: $tab-border-color transparent $tab-border-color $tab-border-color;

		@include linkHover(
			border-color $tab-border-color transparent $tab-border-color $tab-border-color
		);
	}

}//nav-tabs



// Right nav tabs
[class*="col-"].tab-content + [class*="col-"].nav-tabs{
	border-top: none;
	border-right: none;
	border-left: $tab-content-border;


	> li{
		margin: 0 0 0 -1px;
	}// li

	> li > a{
		border-radius: 0 $tab-border-radius $tab-border-radius 0;

		@include linkHover(
			border-color $tab-hover-bg $tab-hover-bg $tab-hover-bg $tab-border-color
		);
	}// > li > a


	> li.active > a, 
	> li.active > a:focus, 
	> li.active > a:hover{
		border-color: $tab-border-color $tab-border-color $tab-border-color transparent;
	}// > li.active > a, a:focus, a:hover
}














