/*---------------------------------------------*/
/*	SECTION ANGLE
/*---------------------------------------------*/
.section-angle{
	position: relative;

	&::before, &::after{
		background: inherit;
		width: 50%;
		height: $sep-angle-height;
		left: 0;
		top: -($sep-angle-top);
		transform: skewY(-($sep-angle));
	}//before / after

	&::after{
		left: 50%;
		transform: skewY($sep-angle);
	}//after



	// BOTTOM
	&.bottom{

		&::before, &::after{
			top: auto;
			bottom: -($sep-angle-top);
			transform: skewY($sep-angle);
		}//before / after

		&::after{
			transform: skewY(-($sep-angle));
		}//after

	}//bottom

}//section-angle






