/*---------------------------------------------------------*/
/*	COLLAPSE
/*---------------------------------------------------------*/

/*---------------------------------------------------------*/
/*	PANEL
/*---------------------------------------------------------*/
.panel{
	background: none;
	border: none;
	margin-bottom: 0;
	border-radius: 0;
	box-shadow: none;


	&:last-child{
		.panel-collapse.in{
			border-bottom: none;
		}//panel-collapse in
	}//last-child

	&:not(:last-child) > .panel-collapse.in{
		border-bottom: solid 1px #ddd;
	}
}//panel



[data-toggle="collapse"]{
	cursor: pointer;
}//[data-toggle="collapse"]



// PANEL COLLAPSE
*:not(.panel){
	.panel-collapse.in:not(:last-child){
		border-bottom: solid 1px #ddd;
	}
}//*:not(.panel)



/*---------------------------------------------------------*/
/*	LIST GROUP ITEM [data-toggle="collapse"]
/*---------------------------------------------------------*/
.list-group-item[data-toggle="collapse"]{
	padding-right: 35px;

	&::before{
		content: '\f3d0';
		opacity: 1;
		transform: translate(0,0);
		transition:.3s;
	}//::before

	&::after{
		content: '\f3d8';
		opacity: 0;
		transform: translate(0,-20px);
		transition:.3s;
	}//::after


	&::before,
	&::after{
		font: 600 14px/0 'Ionicons';
		top: 22px;
		right: 10px;
		position: absolute;
	}//::before, ::after


	&[aria-expanded="true"]::before{
		opacity: 0;
		transform: translate(0,20px);
	}//::before


	&[aria-expanded="true"]::after{
		opacity: 1;
		transform: translate(0,0);
	}//::after


	&:last-child{
		border-bottom: none;
	}//:last-child

}//[data-toggle="collapse"]



/*---------------------------------------------------------*/
/*	CARD COLLAPSING
/*---------------------------------------------------------*/
.card .collapsing{
	width: 100%;
	height: auto;
}//collapsing




