/*---------------------------------------------------------*/
/*	BUTTON MIXINS
/*---------------------------------------------------------*/
@mixin btn-style( 
	$background: 		$btn-background, 
	$border: 				$btn-background, 
	$color: 				$btn-color, 
	$bgHover: 			darken($background, 6%), 
	$borderHover: 	darken($background, 6%), 
	$colorHover: 		$color
){
	background: 	$background; 
	border-color: $border;
	color: 				$color;


	&:hover,
	&:focus,
	&:active,
	&:active:focus,
	&:active:hover{
		background: 	$bgHover; 
		border-color: $borderHover;
		color: 				$colorHover;
		box-shadow: 	none;
	}//:hover


	// .btn-default.active.focus, 
	// .btn-default.active:focus, 
	// .btn-default.active:hover, 
	// .btn-default:active.focus, 
	// .btn-default:active:focus, 
	// .btn-default:active:hover, 
	// .open>.dropdown-toggle.btn-default.focus, 
	// .open>.dropdown-toggle.btn-default:focus, 
	// .open>.dropdown-toggle.btn-default:hover

	&.active,
	&.focus,
	&.active.focus{
		background: 	$bgHover; 
		border-color: $borderHover;
		color: 				$colorHover;
		box-shadow: 	none;


		&:hover,
		&:focus,
		&:active,
		&:active:focus{
			background: 	darken($bgHover, 5%); 
			border-color: darken($borderHover, 5%);
			color: 				$colorHover;
		}//hover
	}// active


	&.disabled, &.disabled:hover, &.disabled:focus, &.disabled:active:focus,
	&[disabled],  &[disabled]:hover, &[disabled]:focus, &[disabled]:active:focus{
		background: 	$background; 
		border-color: $border;
		color: 				$color;
	}//disabled

}// @btn-style






// BTN OUTLINE
@mixin btn-outline-color(
	$color, 
	$hoverFontColor: #fff,
	$background: transparent
){
	background: 	$background; 
	border-color: $color; 
	color: 				$color;

	&:hover{
		background: 	$color; 
		border-color: $color; 
		color: 				$hoverFontColor;
	}

	&.active.focus, &.active:focus, 
	&.active:hover, &.focus:active, 
	&:active:focus, &:active:hover, 
	&:active,
	&:focus,
	&.active{
		background: 	darken($color, 4%); 
		border-color: darken($color, 4%);
		color: 				$hoverFontColor;
	}
}// @btn-outline-color





// CUSTOM COLORED BUTTONS
@mixin btn-color($color){
	background: 	$color; 
	border-color: $color;

	&:hover{
		background: 	lighten($color, 10%); 
		border-color: lighten($color, 10%);
	}//hover

	&:active,
	&:focus,
	&.active{
		background: 	darken($color,20%); 
		border-color: darken($color,20%);
	}
}//btn-color






@mixin btn-dropdown(
	$border-color: 	darken($btn-background, 4%),
	$box-shadow: 		none
){
	&.active.focus, 
	&.active:focus, 
	&.active:hover, 
	&.focus:active, 
	&:active:focus, 
	&:active:hover, 
	&.dropdown-toggle.btn-default.focus, 
	&.dropdown-toggle.btn-default:focus, 
	&.dropdown-toggle.btn-default:hover,

	&.dropdown-toggle.btn-default{
		border-color: 	$border-color;
		box-shadow: 		$box-shadow;
	}//dropdown-toggle btn-default
}










