/*---------------------------------------------------------*/
/*  TABLE
/*---------------------------------------------------------*/
table.table{

	thead{

		> tr{
			> th{
				font-size: $thead-font-size;
				font-weight: $thead-font-weight;
				line-height: $table-line-height;
				border-bottom: solid 1px $table-border;
			}//th
		}//tr 

	}//thead


	tbody{

		> tr{
			> td{
				border-top-color: transparent;
				border-bottom: solid 1px $table-border;
				font-size: $tbody-font-size;
				font-weight: $tbody-font-weight;
				line-height: $table-line-height;
				padding: $table-spacer-y $table-spacer-x;
			}//td
		}//tr 


		> tr:last-child{
			> td{
				border-bottom-color: transparent;
			}//td
		}//tr 

	}//tbody


	tfoot{

		> tr{
			> td{
				font-size: $tfoot-font-size;
				font-weight: $tfoot-font-weight;
				line-height: $table-line-height;
				border-bottom: solid 1px $tfoot-border;
			}//td
		}//tr 


		small{
			font-size: $tfoot-font-size - .1;
		}

	}//tfoot


	p{
		margin-bottom: $table-p-margin;
	}


	.table{
		background: none;
	}//table


	.fa:not(.fa-lg, .fa-2x, .fa-3x, .fa-4x, .fa-5x){
		font-size: 1.3rem;
	}//fa

}//table









/*---------------------------------------------------------*/
/*  TABLE CONDENSED
/*---------------------------------------------------------*/
table.table-condensed{

	thead > tr > th{
		padding-top: $table-condensed-thead-spacer;
		padding-bottom: $table-condensed-thead-spacer;
	}// thead > tr > th


	tbody > tr > td{
		padding-top: $table-condensed-tbody-spacer;
		padding-bottom: $table-condensed-tbody-spacer;
	}// thead > tr > th


	tfoot > tr > td{
		padding-top: $table-condensed-tfoot-spacer;
	}//tfoot > tr > th

}// table table-condensed








/*---------------------------------------------------------*/
/*  TABLE STRIPED
/*---------------------------------------------------------*/
table.table-striped{

	thead > tr > th{
		border-color: $table-striped-border;
	}//thead > tr > th


	tbody{

		> tr{
			background-color: $table-striped-even;

			> td{
				border-color: transparent;
			}//td
		}//tr

		> tr:nth-of-type(2n+1){
			background: $table-striped-odd;
		}//tr:nth-of-type(2n+1)

	}//tbody

}//table-striped 







/*---------------------------------------------------------*/
/*  TABLE STRIPED COLUMNS
/*---------------------------------------------------------*/
table.table-striped-cols{

	thead{
		> tr{
			> th{
				border-bottom-color: transparent;
			}//th
		}//tr
	}//thead


	tbody{
		> tr{

			> td{
				background-color: $table-striped-even;
			}//td

			> td:nth-of-type(2n+1){
				background: $table-striped-odd;
			}//td

		}//tr
	}//tbody

}//table-striped-column







/*---------------------------------------------------------*/
/*  TABLE BORDERED
/*---------------------------------------------------------*/
table.table-bordered{

	thead{
		> tr{
			background: $table-bordered-head-bg;

			> th{
				border-width: 1px;
				border-style: solid;
				border-color: $table-border transparent $table-border $table-border;
			}//th

			> th:last-child{
				border-right: solid 1px $table-border;
			}//th:last-child
		}//tr
	}//thead


	tbody{
		> tr{
			> td{
			}//td
		}//tr

		> tr:last-child > td{
			border-bottom-color: $table-border;
		}// > tr > td:last-child
	}//tbody


	tfoot{
		> tr{
			> td{
				border: solid 1px transparent;
			}//td
		}//tr
	}//tfoot

}//table-bordered







/*---------------------------------------------------------*/
/*  TABLE HOVER
/*---------------------------------------------------------*/
table.table-hover{

	> tbody{
		> tr{
			transition: $table-hover-transition;
		}//tr

		> tr:hover{
			background: $table-hover-bg;

			> td, 
			> th{
				// border-color: transparent;
			}// td, th
		}//tr:hover
	}//tbody

}//table-hover