/*---------------------------------------------------------*/
/*	SIMPLE LIGHTBOX
/*---------------------------------------------------------*/
$sl-info-bg-color: rgba(0,0,0,.5);
$sl-info-color: #fff;
$sl-info-font-size: .9rem;
$sl-btn-height: 100px !default;
$sl-btn-width: 50px !default;
$sl-btn-margin-top: -($sl-btn-height / 2) !default;
$sl-btn-opacity: .4 !default;
$sl-btn-hover-opacity: 1 !default;


$sl-overlay-background: rgba(#fff,.9) !default;


$sl-btn-fill: transparent !default;
$sl-btn-stroke: rgb(0,0,0) !default;
$sl-btn-stroke-width: .5px !default;
$sl-btn-stroke-linecap: round !default;

$sl-btn-close-fill: $sl-btn-fill !default;
$sl-btn-close-stroke: $sl-btn-stroke !default;
$sl-btn-close-stroke-width: 1.5px !default;
$sl-btn-close-stroke-linecap: $sl-btn-stroke-linecap !default;