/*---------------------------------------------------------*/
/*	BOTSTRAP SELECT
/*---------------------------------------------------------*/
.bootstrap-select > .dropdown-menu{
	width: 100%;


		// Search
		.bs-searchbox{
	
			.form-control{
				padding: 3px 10px;
			}//form-control
	
		}//bs-searchbox


		// Check
		.glyphicon.glyphicon-ok.check-mark{
			display: none !important;
		}//glyphicon glyphicon-ok check-mark



		.dropdown-menu.inner{
			width: 100%;

			li{
				font-size: $form-font-size;
				overflow: hidden;

				a{
					padding: 2px 15px;
					white-space: nowrap;
					display: block;
					overflow: hidden;
					text-overflow: ellipsis;
				}//a
			}//li


			.selected > a{
				background: $brand-info;
				color: #fff;
			}//selected

			.hidden{
				display: none;
			}//hidden

		}//dropdown-menu inner


		&.show{
			.dropdown-menu.inner{
				display: block !important;
			}
		}//show


	}//dropdown-menu




	.btn-group.bootstrap-select > .btn.dropdown-toggle{
		padding-top: $form-padding-y;
		padding-bottom: $form-padding-y;
		padding-left: $form-padding-x;


		&.btn-default{
			background: $form-background;
			border-color: $form-border-color;
			color: $form-color;
		}//btn-default


		&:focus{
			outline: 0 none !important;
			outline-offset: 0 !important;
		}//:focus


		&::after{
			border-top-color: $form-border-color;
			border-bottom: none !important;
		}
	
	}//bootstrap-select