/*---------------------------------------------------------*/
/*	ANIMATED ELEMENTS
/*	Combine sliders with Animate CSS to create 
/*	transitions for active slide items
/*---------------------------------------------------------*/
.owl-item{

	.animated{
		animation-name: none;
		-webkit-animation-name: none;
	}//animated


	&:not(.active) .animated{
		animation-name: slide-animated;
		animation-iteration-count: 1;
		animation-duration: .2s;
		animation-delay: 0;
	}



	@keyframes slide-animated{
		0%   { opacity: 1; visibility: visible; }
		100% { opacity: 0; visibility: hidden; }
	}


	&.active{
		.animated{
			visibility: visible;


			&.flash{							-webkit-animation-name:flash; 							animation-name:flash;}
			&.pulse{ 							-webkit-animation-name:pulse; 							animation-name:pulse;}
			&.rubberBand{ 				-webkit-animation-name:rubberBand; 					animation-name:rubberBand;}
			&.shake{ 							-webkit-animation-name:shake; 							animation-name:shake;}
			&.headShake{					-webkit-animation-name:headShake; 					animation-name:headShake;}
			&.swing{							-webkit-animation-name:swing; 							animation-name:swing;}
			&.tada{								-webkit-animation-name:tada; 								animation-name:tada;}
			&.wobble{							-webkit-animation-name:wobble; 							animation-name:wobble;}
			&.jello{							-webkit-animation-name:jello; 							animation-name:jello;}
			&.bounceIn{						-webkit-animation-name:bounceIn; 						animation-name:bounceIn;}
			&.bounceInDown{				-webkit-animation-name:bounceInDown; 				animation-name:bounceInDown;}
			&.bounceInLeft{				-webkit-animation-name:bounceInLeft; 				animation-name:bounceInLeft;}
			&.bounceInRight{			-webkit-animation-name:bounceInRight; 			animation-name:bounceInRight;}
			&.bounceInRight{			-webkit-animation-name:bounceInRight; 			animation-name:bounceInRight;}
			&.bounceInUp{					-webkit-animation-name:bounceInUp; 					animation-name:bounceInUp;}
			&.bounceOut{					-webkit-animation-name:bounceOut; 					animation-name:bounceOut;}
			&.bounceOutDown{			-webkit-animation-name:bounceOutDown; 			animation-name:bounceOutDown;}
			&.bounceOutLeft{			-webkit-animation-name:bounceOutLeft; 			animation-name:bounceOutLeft;}
			&.bounceOutRight{			-webkit-animation-name:bounceOutRight; 			animation-name:bounceOutRight;}
			&.bounceOutUp{				-webkit-animation-name:bounceOutUp; 				animation-name:bounceOutUp;}
			&.fadeIn{							-webkit-animation-name:fadeIn; 							animation-name:fadeIn;}
			&.fadeInDown{					-webkit-animation-name:fadeInDown; 					animation-name:fadeInDown;}
			&.fadeInDownBig{			-webkit-animation-name:fadeInDownBig; 			animation-name:fadeInDownBig;}
			&.fadeInLeft{					-webkit-animation-name:fadeInLeft; 					animation-name:fadeInLeft;}
			&.fadeInLeftBig{			-webkit-animation-name:fadeInLeftBig; 			animation-name:fadeInLeftBig;}
			&.fadeInRight{				-webkit-animation-name:fadeInRight; 				animation-name:fadeInRight;}
			&.fadeInRightBig{			-webkit-animation-name:fadeInRightBig; 			animation-name:fadeInRightBig;}
			&.fadeInUp{						-webkit-animation-name:fadeInUp; 						animation-name:fadeInUp;}
			&.fadeInUpBig{				-webkit-animation-name:fadeInUpBig; 				animation-name:fadeInUpBig;}
			&.fadeOut{						-webkit-animation-name:fadeOut; 						animation-name:fadeOut;}
			&.fadeOutDown{				-webkit-animation-name:fadeOutDown; 				animation-name:fadeOutDown;}
			&.fadeOutDownBig{			-webkit-animation-name:fadeOutDownBig; 			animation-name:fadeOutDownBig;}
			&.fadeOutLeft{				-webkit-animation-name:fadeOutLeft; 				animation-name:fadeOutLeft;}
			&.fadeOutLeftBig{			-webkit-animation-name:fadeOutLeftBig; 			animation-name:fadeOutLeftBig;}
			&.fadeOutRight{				-webkit-animation-name:fadeOutRight; 				animation-name:fadeOutRight;}
			&.fadeOutRightBig{		-webkit-animation-name:fadeOutRightBig; 		animation-name:fadeOutRightBig;}
			&.fadeOutUp{					-webkit-animation-name:fadeOutUp; 					animation-name:fadeOutUp;}
			&.fadeOutUpBig{				-webkit-animation-name:fadeOutUpBig; 				animation-name:fadeOutUpBig;}
			&.flip{								-webkit-animation-name:flip; 								animation-name:flip;}
			&.flipInX{						-webkit-animation-name:flipInX; 						animation-name:flipInX;}
			&.flipInY{						-webkit-animation-name:flipInY; 						animation-name:flipInY;}
			&.flipOutX{						-webkit-animation-name:flipOutX; 						animation-name:flipOutX;}
			&.flipOutY{						-webkit-animation-name:flipOutY; 						animation-name:flipOutY;}
			&.lightSpeedIn{				-webkit-animation-name:lightSpeedIn; 				animation-name:lightSpeedIn;}
			&.lightSpeedOut{			-webkit-animation-name:lightSpeedOut; 			animation-name:lightSpeedOut;}
