/*---------------------------------------------------------*/
/*	SLIDERS
/*---------------------------------------------------------*/
.owl-carousel{

	.owl-stage-outer{
		height: inherit;
		z-index: 5;
	}//owl-stage-outer


	.owl-stage{
		height: inherit;
	}//owl-stage-outer


	.owl-item{
		height: inherit;
		z-index: 1;
	}//owl-item


	.owl-item.active{
		z-index: 10;
	}


	.owl-item > *{
		position: relative;
		z-index: 5;
	}//owl-item

}//owl-carousel




/*---------------------------------------------------------*/
/*	TIMER
/*---------------------------------------------------------*/
.owl-timer{
	background: rgba(#fff, .5);
	height: 3px;
	left: 0;
	right: 0;
	bottom: 0;
	position: absolute;
	z-index: 15;

	.owl-timer-progress{
		background: #fff;
		width: 0;
		height: 100%;
		transition-timing-function: ease-out;
	}
}//owl-timer





@import 'sliders/overlays';
@import 'sliders/animations';
@import 'sliders/dots';
@import 'sliders/arrows';
@import 'sliders/animated-elements';