/*---------------------------------------------------------*/
/*	TOGGLE BUTTONS
/*---------------------------------------------------------*/
.form-check.toggle{
	padding: 0;


	.form-check-input{
		visibility: hidden;
	}//form-check-input


	.form-check-label{

		&::before{
			content: '';
			background-color: #fff;
			width: $toggle-width;
			height: $toggle-height;
			margin-right: 8px;
			display: inline-block;
			vertical-align: middle;
			border-radius: $toggle-height * 10;
			box-shadow: 1px 0 0 2px $toggle-color inset, -17px 0 0 2px $toggle-color inset;
			transition: .3s;
		}//::before

	}//form-check-label


	label{

		span{
			line-height: $toggle-line-height;
		}//span
	}//label



	.form-check-input[type=checkbox]:checked + .form-check-label::before,
	.form-check-input[type=radio]:checked + .form-check-label::before{
		background: #fff;
		box-shadow: 18px 0 0 2px $brand-info inset, 0px 0 0 2px $brand-info inset;
	}

}//form-check