/*---------------------------------------------------------*/
/*	FLOATING LABELS
/*	https://github.com/tonystar/bootstrap-float-label
/*---------------------------------------------------------*/
.has-float-label{
  display: block;
	position: relative;


	.form-control:placeholder-shown:not(:focus) + *{
		font-size: .9rem;
		opacity: .6;
		top: 20px;
		left: 14px;
	}

	label, > span{
		position: absolute;
		left: $form-fl-padding-x;
		top: 5px;
		cursor: text;
		font-size: .7rem;
		margin-bottom: 0;
		opacity: 1;
		transition: all .2s;
		pointer-events: none;
	}//label, > span


	select.form-control{
		-webkit-appearance: none;
		-moz-appearance: none;
		appearance: none;
	}//select

	textarea.form-control{
		width: 100%;
	}

	.form-control{
		padding: $form-fl-padding-top $form-fl-padding-x $form-fl-padding-bottom;
		// font-size: inherit;
		// padding-top: 1em;
		// margin-bottom: 2px;
		// border: 0;
		// border-radius: 0;
		// border-bottom: 2px solid rgba(0, 0, 0, 0.1);
	}//form-control

	.form-control::-webkit-input-placeholder{
		opacity: 1;
		transition: all .2s;
	}//form-control::-webkit-input-placeholder

	.form-control::-moz-placeholder{
		opacity: 1;
		transition: all .2s;
	}//form-control::-moz-placeholder

	.form-control:-ms-input-placeholder{
		opacity: 1;
		transition: all .2s;
	}//form-control:-ms-input-placeholder

	.form-control::placeholder{
		opacity: 1;
		transition: all .2s;
	}//form-control::placeholder
	
	.form-control:placeholder-shown:not(:focus)::-webkit-input-placeholder{
		opacity: 0;
	}
	
	.form-control:placeholder-shown:not(:focus)::-moz-placeholder{
		opacity: 0;
	}
	
	.form-control:placeholder-shown:not(:focus):-ms-input-placeholder{
		opacity: 0;
	}
	
	.form-control:placeholder-shown:not(:focus)::placeholder{
		opacity: 0;
	}


	.form-control:focus{
		// outline: none;
		// border-color: rgba(0, 0, 0, 0.5);
	}
	
	select{
		// padding-right: 1em;
		// background: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 4 5'%3E%3Cpath fill='%23333' d='M2 0L0 2h4zm0 5L0 3h4z'/%3E%3C/svg%3E") no-repeat right 0.5em bottom 0.25em;
		// background-size: 8px 10px;
	}

}//.has-float-label


