/*---------------------------------------------------------*/
/*	CHECKBOX
/*---------------------------------------------------------*/
@mixin checkbox-background-hover($stroke: rgb(255,255,255), $fill: rgb(255,255,255), $stroke-width: 2px, $stroke-linecap: square){
	background-image: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' width='18' height='18' viewBox='0 0 32 32'><path d='M 28.28125 6.28125 L 11 23.5625 L 3.71875 16.28125 L 2.28125 17.71875 L 10.28125 25.71875 L 11 26.40625 L 11.71875 25.71875 L 29.71875 7.71875 L 28.28125 6.28125 z' stroke='#{$stroke}' fill='#{$fill}' stroke-width='#{$stroke-width}' stroke-linecap='#{$stroke-linecap}'/></svg>");
}


.form-check.custom{
	padding: 0;


	.form-check-input{
		visibility: hidden;
		position: absolute;
	}//form-check-input


	.form-check-input[type=checkbox] + .form-check-label{

		&::before{
			content: ''; 
			background-color: #e4e4e4;
			background-repeat: no-repeat;
			background-size: 80%;
			background-position: center;
			width: $checkbox-size;
			height: $checkbox-size;
			margin-right: 8px;
			display: inline-block;
			vertical-align: middle;
			border-radius: $global-radius;
			transition: .2s;
		}//::before

	}//.form-check-label


	.form-check-input[type=checkbox]:checked + .form-check-label::before{
		background-color: $brand-info;
		@include checkbox-background-hover;
	}//.form-check-input[type=checkbox]:checked + .form-check-label::after


	.form-check-input[type=checkbox][disabled] + .form-check-label::before{
		background-color: #eee;
	}

}//.form-check.checkbox