/*---------------------------------------------*/
/*	SECTION TRIANGLE
/*---------------------------------------------*/
.section-triangle{
	position: relative;

	&::before, &::after{
		width: $sep-triangle-width;
		height: $sep-triangle-height;
		left: 50%;
		transform: translateX(-($sep-triangle-width /2)) rotate(45deg);
	}//before / after

	&::before{
		background: $sep-triangle-bg;
		top: -($sep-triangle-height /2);
	}//before

	&::after{
		background: $sep-triangle-bg;
		bottom: -($sep-triangle-height /2);
		z-index: 10;
	}//after

	&.top::after{display: none;}
	&.bottom::before{display: none;}

}//section-triangle




