/*---------------------------------------------------------*/
/*	DOTS NAVIGATION
/*---------------------------------------------------------*/
.owl-dots{
	padding-left: 15px;
	padding-right: 15px;
	left: 0;
	right: 0;
	bottom: 10px;
	position: absolute;
	display: flex;
	justify-content: center;
	pointer-events: none;
	z-index: 15;



	.owl-dot{
		margin: 0 5px;
		display: inline-flex;


		&:hover, &:active, &:focus, &:focus:active, &.active{
			background: transparent;
		}

	}//owl-dot



	.owl-dot > span{
		background: #fff;
		width: 13px;
		height: 13px;
		display: flex;
		align-items: center;
		justify-content: center;
		position: relative;
		transition: .3s ease-in-out;
		border-radius: 1000px;
		opacity: .6;
		pointer-events: initial;
		cursor: pointer;
	}//owl-dot



	.owl-dot.active > span{
		opacity: 1;
	}//owl-dot.active > span

}//owl-dots






.owl-has-right-dots{
	.owl-dots{
		justify-content: flex-end;
	}//owl-dots
}//owl-has-right-dots






.owl-has-left-dots{
	.owl-dots{
		justify-content: flex-end;
	}//owl-dots
}//owl-has-right-dots