/*-----------------------------------------------*/
/*	RESPONSIVE
/*	Bootstrap media queries
/*-----------------------------------------------*/
@media screen and (max-width: 768px) {

	/*---------------------------------------------------------*/
	/*	VARIABLES
	/*---------------------------------------------------------*/
	$font-h1-font-size: 1.8rem;
	$font-h2-font-size: 1.5rem;
	$font-h3-font-size: 1.3rem;
	$font-h4-font-size: 1.1rem;
	$font-h5-font-size: 1rem;
	$font-h6-font-size: .8rem;


	/*---------------------------------------------------------*/
	/*	HEADS
	/*---------------------------------------------------------*/
	h1, .h1{ font-size: $font-h1-font-size; }
	h2, .h2{ font-size: $font-h2-font-size; }
	h3, .h3{ font-size: $font-h3-font-size; }
	h4, .h4{ font-size: $font-h4-font-size; }
	h5, .h5{ font-size: $font-h5-font-size; }
	h6, .h6{ font-size: $font-h6-font-size; }


	/*---------------------------------------------------------*/
	/*	DISPLAY HEADINGS
	/*---------------------------------------------------------*/
	.display-1{ font-size: $display-1-fz - ($display-1-fz /1.7); }
	.display-2{ font-size: $display-2-fz - ($display-2-fz /2); }
	.display-3{ font-size: $display-3-fz - ($display-3-fz /2.3); }
	.display-4{ font-size: $display-4-fz - ($display-4-fz /2.5); }


	/*---------------------------------------------------------*/
	/*	DISPLAY HEADINGS
	/*---------------------------------------------------------*/
	.navbar-toggle.collapsed{
		display: block;
	}//navbar-toggle






}//max-width: 768px








@media screen and (max-width: 480px) {
	
	/*---------------------------------------------------------*/
	/*	VARIABLES
	/*---------------------------------------------------------*/
	$spacer-sm: .3rem;
	$spacer:    .8rem;
	$spacer-md: .8rem;
	$spacer-lg: 1.1rem;


	/*---------------------------------------------------------*/
	/*	PADDING
	/*---------------------------------------------------------*/
	$multiplier: .6;
	.p-md,
	.p-a-md { padding:        ($spacer * $multiplier) !important; }
	.p-t-md { padding-top:    ($spacer * $multiplier) !important; }
	.p-r-md { padding-right:  ($spacer * $multiplier) !important; }
	.p-b-md { padding-bottom: ($spacer * $multiplier) !important; }
	.p-l-md { padding-left:   ($spacer * $multiplier) !important; }
	.p-x-md { padding-right:  ($spacer * $multiplier) !important; padding-left:   ($spacer * $multiplier) !important; }
	.p-y-md { padding-top:    ($spacer * $multiplier) !important; padding-bottom: ($spacer * $multiplier) !important; }

	.m-a-md { margin:        ($spacer * $multiplier) !important; }
	.m-t-md { margin-top:    ($spacer * $multiplier) !important; }
	.m-r-md { margin-right:  ($spacer * $multiplier) !important; }
	.m-b-md { margin-bottom: ($spacer * $multiplier) !important; }
	.m-l-md { margin-left:   ($spacer * $multiplier) !important; }
	.m-x-md { margin-right:  ($spacer * $multiplier) !important; margin-left:   ($spacer * $multiplier) !important; }
	.m-y-md { margin-top:    ($spacer * $multiplier) !important; margin-bottom: ($spacer * $multiplier) !important; }


	$multiplier: 1.1;
	.p-lg,
	.p-a-lg { padding:        ($spacer * $multiplier) !important; }
	.p-t-lg { padding-top:    ($spacer * $multiplier) !important; }
	.p-r-lg { padding-right:  ($spacer * $multiplier) !important; }
	.p-b-lg { padding-bottom: ($spacer * $multiplier) !important; }
	.p-l-lg { padding-left:   ($spacer * $multiplier) !important; }
	.p-x-lg { padding-right:  ($spacer * $multiplier) !important; padding-left:   ($spacer * $multiplier) !important; }
	.p-y-lg { padding-top:    ($spacer * $multiplier) !important; padding-bottom: ($spacer * $multiplier) !important; }

	.m-a-lg { margin:        ($spacer * $multiplier) !important; }
	.m-t-lg { margin-top:    ($spacer * $multiplier) !important; }
	.m-r-lg { margin-right:  ($spacer * $multiplier) !important; }
	.m-b-lg { margin-bottom: ($spacer * $multiplier) !important; }
	.m-l-lg { margin-left:   ($spacer * $multiplier) !important; }
	.m-x-lg { margin-right:  ($spacer * $multiplier) !important; margin-left:   ($spacer * $multiplier) !important; }
	.m-y-lg { margin-top:    ($spacer * $multiplier) !important; margin-bottom: ($spacer * $multiplier) !important; }


	@for $i from 1 through 9{

		.p-a-lg-#{$i}{
			padding: ($spacer-lg * $i) + $spacer-lg !important;
		}

		.p-t-lg-#{$i}{
			padding-top: ($spacer-lg * $i) + $spacer-lg !important;
		}

		.p-r-lg-#{$i}{
			padding-right: ($spacer-lg * $i) + $spacer-lg !important;
		}

		.p-b-lg-#{$i}{
			padding-bottom: ($spacer-lg * $i) + $spacer-lg !important;
		}

		.p-l-lg-#{$i}{
			padding-left: ($spacer-lg * $i) + $spacer-lg !important;
		}

		.p-x-lg-#{$i}{
			padding-left: ($spacer-lg * $i) + $spacer-lg !important;
			padding-right: ($spacer-lg * $i) + $spacer-lg !important;
		}

		.p-y-lg-#{$i}{
			padding-top: ($spacer-lg * $i) + $spacer-lg !important;
			padding-bottom: ($spacer-lg * $i) + $spacer-lg !important;
		}

		.m-a-lg-#{$i}{
			margin: ($spacer-lg * $i) + $spacer-lg !important;
		}

		.m-t-lg-#{$i}{
			margin-top: ($spacer-lg * $i) + $spacer-lg !important;
		}

		.m-r-lg-#{$i}{
			margin-right: ($spacer-lg * $i) + $spacer-lg !important;
		}

		.m-b-lg-#{$i}{
			margin-bottom: ($spacer-lg * $i) + $spacer-lg !important;
		}

		.m-l-lg-#{$i}{
			margin-left: ($spacer-lg * $i) + $spacer-lg !important;
		}

		.m-x-lg-#{$i}{
			margin-left: ($spacer-lg * $i) + $spacer-lg !important;
			margin-right: ($spacer-lg * $i) + $spacer-lg !important;
		}

		.m-y-lg-#{$i}{
			margin-top: ($spacer-lg * $i) + $spacer-lg !important;
			margin-bottom: ($spacer-lg * $i) + $spacer-lg !important;
		}

	}

}//max-width: 440px



