/*---------------------------------------------------------*/
/*	BACKGROUND COLORS
/*---------------------------------------------------------*/
@mixin bg-color($color){
	background-color: $color !important;
}

/*---------------------------------------------------------*/
/*	BOOTSTRAP SEMANTIC COLORS
/*---------------------------------------------------------*/
// PRIMARY
.bg-primary-darker 		{@include bg-color($brand-primary-darker);}
.bg-primary-dark 			{@include bg-color($brand-primary-dark);}
.bg-primary 					{@include bg-color($brand-primary);}
.bg-primary-light 		{@include bg-color($brand-primary-light);}
.bg-primary-lighter 	{@include bg-color($brand-primary-lighter);}


// INFO
.bg-info-darker				{@include bg-color($brand-info-darker);}
.bg-info-dark					{@include bg-color($brand-info-dark);}
.bg-info							{@include bg-color($brand-info);}
.bg-info-light				{@include bg-color($brand-info-light);}
.bg-info-lighter			{@include bg-color($brand-info-lighter);}


// SUCCESS
.bg-success-darker		{@include bg-color($brand-success-darker);}
.bg-success-dark			{@include bg-color($brand-success-dark);}
.bg-success						{@include bg-color($brand-success);}
.bg-success-light			{@include bg-color($brand-success-light);}
.bg-success-lighter		{@include bg-color($brand-success-lighter);}


// WARNING
.bg-warning-darker		{@include bg-color($brand-warning-darker);}
.bg-warning-dark			{@include bg-color($brand-warning-dark);}
.bg-warning						{@include bg-color($brand-warning);}
.bg-warning-light			{@include bg-color($brand-warning-light);}
.bg-warning-lighter		{@include bg-color($brand-warning-lighter);}


// DANGER
.bg-danger-darker			{@include bg-color($brand-danger-darker);}
.bg-danger-dark				{@include bg-color($brand-danger-dark);}
.bg-danger 						{@include bg-color($brand-danger);}
.bg-danger-light			{@include bg-color($brand-danger-light);}
.bg-danger-lighter		{@include bg-color($brand-danger-lighter);}


// GRAY
.bg-gray-darker 	{@include bg-color($gray-darker);}
.bg-gray-dark 		{@include bg-color($gray-dark);}
.bg-gray			 		{@include bg-color($gray);}
.bg-gray-light 		{@include bg-color($gray-light);}
.bg-gray-lighter 	{@include bg-color($gray-lighter);}

.bg-transparent 	{@include bg-color(transparent);}
.bg-none 					{background: none !important;}



// WHITE
.bg-white  					{@include bg-color(#fff); }
.bg-white-1 				{@include bg-color( rgba(#fff, .1) ); }
.bg-white-2 				{@include bg-color( rgba(#fff, .2) ); }
.bg-white-3 				{@include bg-color( rgba(#fff, .3) ); }
.bg-white-4 				{@include bg-color( rgba(#fff, .4) ); }
.bg-white-5 				{@include bg-color( rgba(#fff, .5) ); }
.bg-white-6 				{@include bg-color( rgba(#fff, .6) ); }
.bg-white-7 				{@include bg-color( rgba(#fff, .7) ); }
.bg-white-8 				{@include bg-color( rgba(#fff, .8) ); }
.bg-white-9 				{@include bg-color( rgba(#fff, .9) ); }

// BLACK
.bg-black  					{@include bg-color(#000); }
.bg-black-1 				{@include bg-color( rgba(#000, .1) ); }
.bg-black-2 				{@include bg-color( rgba(#000, .2) ); }
.bg-black-3 				{@include bg-color( rgba(#000, .3) ); }
.bg-black-4 				{@include bg-color( rgba(#000, .4) ); }
.bg-black-5 				{@include bg-color( rgba(#000, .5) ); }
.bg-black-6 				{@include bg-color( rgba(#000, .6) ); }
.bg-black-7 				{@include bg-color( rgba(#000, .7) ); }
.bg-black-8 				{@include bg-color( rgba(#000, .8) ); }
.bg-black-9 				{@include bg-color( rgba(#000, .9) ); }






/*---------------------------------------------------------*/
/*	TEXT COLORS
/*---------------------------------------------------------*/
@mixin text-color($color){
	color: $color !important;
}


// TEXT PRIMARY
.text-primary-darker 	{@include text-color($brand-primary-darker);}
