/*---------------------------------------------*/
/*	SECTION ANGLE
/*---------------------------------------------*/
.section-curve{
	position: relative;

	&::before{
		background: inherit;
		width: 100%;
		height: $sep-curve-height;
		left: 0;
		top: -($sep-curve-height /2);
		border-radius: 50% 50% 0 0;
	}//before / after


	// BOTTOM
	&.bottom{

		&::before{
			top: auto;
			bottom: -($sep-curve-height /2);
			border-radius: 0 0 50% 50%;
		}// before

	}//bottom

}//section-curve