/*---------------------------------------------------------*/
/*	FAKE SCROLL
/*---------------------------------------------------------*/

.scrollWrap{
	overflow: 	hidden;
	height: 		100%;
	position: 	relative;
	z-index: 		1;
}//scrollWrap


.scrollContent{
	height: 				100%;
	width: 					calc(100% + 18px);
	padding-right: 	5px;
	// padding-right: 	25px;
	position: 			relative;
	overflow: 			auto;
}//scrollContent

.fakeScrollBar{
	position: 			absolute;
	background: 		rgba(#000,.1);
	width: 					5px;
	border-radius: 	4px;
	right: 					0;
	top: 						0;
	opacity: 				0;
	transition: 		.3s;
	z-index: 				3;
	cursor: 				-moz-grab;
	cursor: 				-webkit-grab;
}//fakeScrollBar

.scrollWrap:hover + .fakeScrollBar{
	opacity: 1;
}

.fakeScroll:hover .fakeScrollBar.fakescroll-grabbed{
	cursor: 		-moz-grabbing;
	cursor: 		-webkit-grabbing;
	background: #FFF;
}

body.fakescroll-grabbed{
	cursor: -moz-grabbing;
	cursor: -webkit-grabbing;
}


