.owl-carousel .owl-nav{
	width: 100%;
	height: 100%;
	top: 0;
	position: absolute;
	pointer-events: none;
	z-index: 15;


	button.owl-prev, button.owl-next{
		background-repeat: no-repeat;
		background-position: center;
		background-size: 26px auto;
		color: transparent;
		font-size: 0;
		width: 100px;
		height: 100%;
		top: 0;
		position: absolute;
		opacity: 0;
		visibility: hidden;
		transition: .4s ease-in-out;
		pointer-events: initial;
		cursor: pointer;
	}//owl-prev, owl-next


	button.owl-prev{ 
		background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='36px' height='64px' viewBox='0 0 36 64' version='1.1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink'%3e%3cg id='Page-1' stroke='none' stroke-width='1' fill='none' fill-rule='evenodd'%3e%3cpolygon id='angle-left' fill='%23FFF' fill-rule='nonzero' points='35.999451 3.999451 7.999451 31.999451 35.999451 59.999451 31.8883399 63.999451 0.0005496 31.999451 31.8883399 -0.000549'%3e%3c/polygon%3e%3c/g%3e%3c/svg%3e");
		background-position: 15px center;
		left: -20px;
	}//owl-prev


	button.owl-next{ 
		background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='36px' height='64px' viewBox='0 0 36 64' version='1.1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink'%3e%3cg id='Page-1' stroke='none' stroke-width='1' fill='none' fill-rule='evenodd'%3e%3cpolygon id='angle-left' fill='%23FFF' fill-rule='nonzero' points='0.0005496 3.999451 4.1116607 -0.000549 35.999451 31.999451 4.1116607 63.999451 0.0005496 59.999451 28.0005496 31.999451'%3e%3c/polygon%3e%3c/g%3e%3c/svg%3e");
		background-position: 60px center;
		right: -20px;
	}//owl-next

}//owl-nav


.owl-carousel:hover .owl-nav .owl-prev,
.owl-carousel:hover .owl-nav .owl-next{
	opacity: 1;
	visibility: visible;
}

.owl-carousel:hover .owl-nav .owl-prev{ left: 0; }

.owl-carousel:hover .owl-nav .owl-next{ right: 0; }