/*---------------------------------------------------------*/
/*	OFF CANVAS
/*	Additional styles for slidebars.js plugin
/*---------------------------------------------------------*/

$offcanvas-transition: .3s !default;

// WRAPPER
.off-canvas-wrapper{
	width: 100%;
	min-height: 100%;
	overflow-x: hidden;
}//off-canvas-wrapper




// SIDEBAR
.off-canvas-sidebar{
	height: 100vh;
	width: 280px;
	left: 0;
	top: 0;
	position: fixed;
	transition: $offcanvas-transition;
	transform: translate3d(-100%,0,0);
	transform-origin: 100% 50%;
	z-index: 4;
}//off-canvas-sidebar


.off-canvas-wrapper.in .off-canvas-sidebar{
	transform: translate3d(0,0,0) !important;
}



// CONTAINER
.off-canvas-container{
	// height: 5000px;
	position: relative;
	z-index: 2;
}//off-canvas-container






/*---------------------------------------------------------*/
/*	REVEAL
/*---------------------------------------------------------*/
.off-canvas-wrapper.reveal{

	// SIDEBAR
	.off-canvas-sidebar{
		transform: translate3d(0,0,0);
		z-index: 1;
	}//off-canvas-sidebar


	// CONTAINER
	.off-canvas-container{
		background: #f9f9f9;
		transition: $offcanvas-transition;
		transform: translate3d(0,0,0);
	}//off-canvas-container


	&.in .off-canvas-container{
		transform: translate3d(280px,0,0);
	}

}//reveal








/*---------------------------------------------------------*/
/*	PUSH
/*---------------------------------------------------------*/
.off-canvas-wrapper.push{

	// SIDEBAR
	.off-canvas-sidebar{
		transform: translate3d(-100%,0,0);
		z-index: 4;
	}//off-canvas-sidebar


	// CONTAINER
	.off-canvas-container{
		background: #f9f9f9;
		transition: $offcanvas-transition;
		transform: translate3d(0,0,0);
	}//off-canvas-container


	&.in .off-canvas-container{
		transform: translate3d(280px,0,0);
	}

	&.in .off-canvas-sidebar{
		transform: translate3d(0,0,0);
	}

}//push







/*---------------------------------------------------------*/
/*	SLIDE ALONG
/*---------------------------------------------------------*/
.off-canvas-wrapper.slide-along{

	// SIDEBAR
	.off-canvas-sidebar{
		transform: translate3d(-50%,0,0);
		z-index: 1;
	}//off-canvas-sidebar


	// CONTAINER
	.off-canvas-container{
		background: #f9f9f9;
		transition: $offcanvas-transition + $offcanvas-transition/4;
		transform: translate3d(0,0,0);
	}//off-canvas-container


	&.in .off-canvas-container{
		transform: translate3d(280px,0,0);
	}

	&.in .off-canvas-sidebar{
		transform: translate3d(0,0,0);
	}

}//slide-along








/*---------------------------------------------------------*/
/*	ROTATE PUSH
/*---------------------------------------------------------*/
.off-canvas-wrapper.rotate-push{
	perspective: 1500px;

	// SIDEBAR
	.off-canvas-sidebar{
		transform: translate3d(-100%,0,0);
		z-index: 1;
	}//off-canvas-sidebar


	// CONTAINER
	.off-canvas-container{
		background: #f9f9f9;
		transition: $offcanvas-transition;
		transform: translate3d(0,0,0);
		transform-origin: 0 50%;
		transform-style: preserve-3d;
	}//off-canvas-container


	&.in .off-canvas-container{
		transform: translate3d(280px,0,0) rotateY(-30deg);
	}

	&.in .off-canvas-sidebar{
		transform: translate3d(0,0,0);
	}

}//rotate-push








/*---------------------------------------------------------*/
/*	SCALE
/*---------------------------------------------------------*/
.off-canvas-wrapper.scale{

	// SIDEBAR
	.off-canvas-sidebar{
		transform: translate3d(-50%,0,0) scale(.9);
		z-index: 1;
	}//off-canvas-sidebar


	// CONTAINER
	.off-canvas-container{
		background: #f9f9f9;
		transition: $offcanvas-transition;
		transform: translate3d(0,0,0);
	}//off-canvas-container


	&.in .off-canvas-container{
		transform: translate3d(280px,0,0);
	}

	&.in .off-canvas-sidebar{
		transform: translate3d(0,0,0) scale(1);
		// transform: translate3d(0,0,0);
	}

}//scale






/*---------------------------------------------------------*/
/*	ROTATE SCALE
/*---------------------------------------------------------*/
.off-canvas-wrapper.rotate-scale{
	perspective: 1500px;

	// SIDEBAR
	.off-canvas-sidebar{
		transform: translate3d(-100%,0,0);
	}//off-canvas-sidebar


	// CONTAINER
	.off-canvas-container{
		background: #f9f9f9;
		transition: $offcanvas-transition;
		transform: translate3d(0,0,0);
		transform-origin: 0 50%;
		transform-style: preserve-3d;
	}//off-canvas-container


	&.in .off-canvas-container{
		transform: translate3d(100px,0,-600px) rotateY(-20deg);
	}

	&.in .off-canvas-sidebar{
		transform: translate3d(0,0,0);
	}

}//rotate-scale









/*---------------------------------------------------------*/
/*	OVERLAY
/*---------------------------------------------------------*/
.off-canvas-overlay{
	width: 100%;
	height: 100%;
	left: 0;
	top: 0;
	position: fixed;
	visibility: hidden;
	opacity: 0;
	pointer-events: none;
	transition: $offcanvas-transition;
	z-index: 3;
}//off-canvas-overlay


.off-canvas-wrapper.in .off-canvas-overlay{
	background: rgba(#000,.1);
	visibility: visible;
	opacity: 1;
	pointer-events: inherit;
}//off-canvas-overlay



