// https://dvcs.w3.org/hg/FXTF/raw-file/tip/filters/index.html 



// blur           ex: @include filter: blur(2px);
@mixin filter-blur($val){
	-webkit-filter: blur($val);
	-moz-filter: 		blur($val);
	-o-filter: 			blur($val);
	-ms-filter: 		blur($val);
	filter: 				blur($val);
}



// contrast       ex: @include filter: contrast(200%);
@mixin filter-contrast($val){
	-webkit-filter: contrast($val);
	-moz-filter: 		contrast($val);
	-o-filter: 			contrast($val);
	-ms-filter: 		contrast($val);
	filter: 				contrast($val);
}



