// Filters for background:
// http://sassbreak.com/css-blend-modes-with-sass/
//
// Blend Values:
// 		- normal (default)
// 		- multiply
// 		- luminosity
// 		- lighten
// 		- hue
// 		- hard-light
// 		- exclusion
// 		- difference
// 		- darken
// 		- color-dodge
// 		- color-burn
// 		- color
// 		- unset
// 		- softlight
// 		- screen
// 		- saturation
// 		- overlay
@mixin blendy($img, $color: null, $grad: null, $blend: multiply){
  $img-path: url('img/#{$img}');
  @if $grad {
    background: $grad, $img-path;
  } @else {
    background-image: $img-path;
    background-color: $color;
  }
  background-blend-mode: $blend;
}

