/*---------------------------------------------------------*/
/*  GRADIENTS
/*  Inspired in "Hue: Promo Backdrops and Gradients" 
/*  by Vladimir Kudinov
/*  https://www.behance.net/gallery/30067997/Hue-Free-Promo-Backdrops-and-Gradients
/*---------------------------------------------------------*/
.gradient{
	position: relative;


	@mixin after(
		$color-start: #000,
		$color-end: #000,
		$deg: 180deg,
		$width: 100%,
		$height: 100%,
		$left: 0,
		$right: auto,
		$bottom: 0,
		$top: auto
	){
		content: '';
		@include gradient-directional(rgba($color-start,0), rgba($color-end,.95), $deg);
		width: $width;
		height: $height;
		left: $left;
		right: $right;
		top: $top;
		bottom: $bottom;
		position: absolute;
		z-index: 1;
	}



	&.overlay-gradient-dark{

		&::after{@include after();}
		&.gradient-50::after{@include after(#000, #000, 180deg, 100%, 50%);}
		&.gradient-top::after{@include after(#000, #000, 360deg, 100%, 100%, 0, auto, auto, 0);}
		&.gradient-top.gradient-50::after{@include after(#000, #000, 360deg, 100%, 50%, 0, auto, auto, 0);}
		&.gradient-left::after{@include after(#000, #000, 270deg, 100%, 100%);}
		&.gradient-left.gradient-50::after{@include after(#000, #000, 270deg, 50%, 100%);}
		&.gradient-right::after{@include after(#000, #000, 90deg, 100%, 100%);}
		&.gradient-right.gradient-50::after{@include after(#000, #000, 90deg, 50%, 100%, auto, 0);}

	}//overlay-gradient-dark::after




	&.overlay-gradient-light{

		&::after{@include after(#fff, #fff);}
		&.gradient-50::after{@include after(#fff, #fff, 180deg, 100%, 50%);}
		&.gradient-top::after{@include after(#fff, #fff, 360deg, 100%, 100%, 0, auto, auto, 0);}
		&.gradient-top.gradient-50::after{@include after(#fff, #fff, 360deg, 100%, 50%, 0, auto, auto, 0);}
		&.gradient-left::after{@include after(#fff, #fff, 270deg, 100%, 100%);}
		&.gradient-left.gradient-50::after{@include after(#fff, #fff, 270deg, 50%, 100%);}
		&.gradient-right::after{@include after(#fff, #fff, 90deg, 100%, 100%);}
		&.gradient-right.gradient-50::after{@include after(#fff, #fff, 90deg, 50%, 100%, auto, 0);}

	}//overlay-gradient-dark::after


}//gradient