/*---------------------------------------------------------*/
/*	TABLES
/*---------------------------------------------------------*/
$table-border: lighten($gray-light,10%) !default;
$table-line-height: 1.4 !default;
$table-spacer-y: 15px !default;
$table-spacer-x: 10px !default;

$thead-font-size: .9rem !default;
$thead-font-weight: 500 !default;

$tbody-font-size: .95rem !default;
$tbody-font-weight: inherit !default;

$tfoot-font-size: .8rem !default;
$tfoot-font-weight: inherit !default;
$tfoot-border: transparent !default;



// CONDENSED
$table-condensed-thead-spacer: 8px !default;
$table-condensed-tbody-spacer: 7px !default;
$table-condensed-tfoot-spacer: 10px !default;



// STRIPED
$table-striped-even: transparent !default;
$table-striped-odd: $gray-lighter !default;
$table-striped-border: transparent !default;


// BORDERED
$table-bordered-head-bg: $gray-lighter !default;


// HOVER
$table-hover-transition: .3s !default;
$table-hover-bg: darker($gray-lighter, 5%) !default;



$table-p-margin: 10px !default;









