/*---------------------------------------------------------*/
/*	COLORS
/*---------------------------------------------------------*/
$white: 						#fff !default;
$black: 						#000 !default;

// -- BOOTSTRAP COLORS
$gray-darker: 			lighten(#000, 20%) !default;
$gray-dark: 				lighten(#000, 40%) !default;
$gray: 							lighten(#000, 60%) !default;
$gray-light: 				lighten(#000, 80%) !default;
$gray-lighter: 			lighten(#000, 95%) !default;



/*---------------------------------------------------------*/
/*	BOOTSTRAP SEMANTIC COLORS
/*---------------------------------------------------------*/
// PRIMARY
$brand-primary: 			#227FBB !default;
$brand-primary-darker:	darken($brand-primary, 20%) !default;
$brand-primary-dark: 		darken($brand-primary, 10%) !default;
$brand-primary-light: 	lighten($brand-primary, 15%) !default;
$brand-primary-lighter: lighten($brand-primary, 30%) !default;


// SUCCESS
$brand-success: 		#1ECD6D !default;
$brand-success-darker:	darken($brand-success, 20%) !default;
$brand-success-dark: 		darken($brand-success, 10%) !default;
$brand-success-light: 	lighten($brand-success, 15%) !default;
$brand-success-lighter: lighten($brand-success, 30%) !default;


// INFO
