$brand-info-darker:	darken($brand-info, 20%) !default;
$brand-info-dark: 		darken($brand-info, 10%) !default;
$brand-info-light: 	lighten($brand-info, 15%) !default;
$brand-info-lighter: lighten($brand-info, 30%) !default;


// WARNING
$brand-warning: 		#F59D00 !default;
$brand-warning-darker:	darken($brand-warning, 20%) !default;
$brand-warning-dark: 		darken($brand-warning, 10%) !default;
$brand-warning-light: 	lighten($brand-warning, 15%) !default;
$brand-warning-lighter: lighten($brand-warning, 30%) !default;


// DANGER
$brand-danger: 			#C23824 !default;
$brand-danger-darker:	darken($brand-danger, 20%) !default;
$brand-danger-dark: 		darken($brand-danger, 10%) !default;
$brand-danger-light: 	lighten($brand-danger, 15%) !default;
$brand-danger-lighter: lighten($brand-danger, 30%) !default;


