// h-sm-*
@media (min-width: 576px) {

	@for $i from 1 through 100{
		.h-sm-#{$i}per{
			height: $height-percent-val * $i !important;
		}
		
		.h-sm-#{$i}0px{
			height: $height-val * $i !important;
		}
	}

}



