/*---------------------------------------------------------*/
/*	FORMS
/*---------------------------------------------------------*/
form{
	position: 						relative;
}//form


label{
	color: 								$form-label-color; 
	font-size: 						$form-label-font-size; 
	font-weight: 					$form-label-font-weight;
}//label


.form-group{
	position: 						relative;


	// HELP
	.help{
		font-size: 					0.75rem;
		color: 							$form-help-color;
	}//help

	.help-block{
		font-size: 					0.85rem;
		color: 							$form-help-color;
	}//help-block

}//form-group





/*---------------------------------------------------------*/
/*	FORM CONTROL
/*---------------------------------------------------------*/
.form-control{
	background: 					$form-background;
	border: 							solid $form-border $form-border-color; 
	color: 								$form-color;
	font-size: 						$form-font-size;
	line-height: 					$form-line-height;
	height: 							auto;
	padding: 							$form-padding-y $form-padding-x;
	transition: 					.3s;
	border-radius: 				$global-radius;
	box-shadow: 					none;
	resize: 							none;




	&[disabled], 
	&[readonly]{
		opacity: 						$form-disabled-opacity;
	}

	@include placeholder{
		color: 							$form-placeholder-color;
	}
}//form-control


.form-control:focus{
	background: 					$form-background-focus;
	border-color: 				$form-border-color-focus;
	box-shadow: 					none;

	@include placeholder{
		color: 							$form-placeholder-color-focus;
	}
}//form-control:focus



// ROUNDED FORMS
.input-rounded{
	border-radius: 				$form-rounded-radius;
}//input-rounded



select.form-control:not([size]):not([multiple]){
	background-image: 		url('data:image/png;base64,');
	background-repeat: 		no-repeat;
	background-position: 	center right;
	background-size: 			23px auto;
	height: 							auto;
	-webkit-appearance: 	none;
	-moz-appearance: 			none;
	appearance: 					none;
}//select.form-control


select[multiple].form-control:not([size]){
	padding-left: 				0;
	padding-right: 				0;

	option{
		padding: 						2px $form-padding-x;
	}
}//select multiple









/*---------------------------------------------------------*/
/*	INPUT SIZES
/*---------------------------------------------------------*/
.form-control-sm, 
.form-horizontal .form-group-sm .form-control{
	font-size: 						$form-sm-font-size; 
	padding: 							$form-sm-padding-y $form-sm-padding-x;
}


.form-control-lg, 
.form-horizontal .form-group-lg .form-control{
	font-size: 						$form-lg-font-size;
	padding: 							$form-lg-padding-y $form-lg-padding-x;
}






@import 'forms/input-group';
@import 'forms/validation-states';
@import 'forms/checkbox';
@import 'forms/radio-buttons';
@import 'forms/toggle-buttons';
@import 'forms/bootstrap-select';
@import 'forms/touchspin';
@import 'forms/datepicker';
@import 'forms/range';
@import 'forms/float-labels';