/*---------------------------------------------------------*/
/*	SEPARATORS
/*---------------------------------------------------------*/

// TRIANGLE
$sep-triangle-bg: inherit !default;
$sep-triangle-width: 80px !default;
$sep-triangle-height: 80px !default;


// DIAGONAL
$sep-diagonal-spacer-top: 20% !default;
$sep-diagonal-top: 10% !default;
$sep-diagonal-height: 75% !default;
$sep-diagonal-angle: 3deg !default;


// DOUBLE DIAGONAL
$sep-double-diagonal-spacer-top: 20% !default;
$sep-double-diagonal-top: 10% !default;
$sep-double-diagonal-height: 75% !default;
$sep-double-diagonal-front-angle: 3deg !default;
$sep-double-diagonal-back-angle: 4deg !default;
$sep-double-diagonal-back-angle-origin: 35% !default;
$sep-double-diagonal-back-angle-reverse-origin: 65% !default;


// ANGLE
$sep-angle-height: 30% !default;
$sep-angle-top: 10% !default;
$sep-angle: 6deg !default;


// CURVE
$sep-curve-height: 60% !default;



