/*---------------------------------------------------------*/
/*	INPUT GROUP
/*---------------------------------------------------------*/
.input-group{
	
	.form-control, 
	.input-group-btn{
		border-color: $form-border-color;
		font-size: $form-font-size;
		height: $form-ig-btn-size;
	}


	.input-group-addon{
		margin-left: -1px;
		margin-right: -1px;
		display: flex;
	}//input-group-addon


	.input-group-prepend,
	.input-group-append{ 
		z-index: 3;

		.btn{
			height: $form-ig-btn-size;
			padding: 0 $form-padding-x;
			
			
			&.btn-icon{
				font-size: $form-ig-btn-icon;
				width: $form-ig-btn-size + 4;
			}//btn-icon
		}//btn


	}//input-group-prepend, input-group-append


	.input-rounded{ border-radius: $form-rounded-radius; }
	.btn.btn-rounded{ border-radius: 1000px; }




	// SM
	&.input-group-sm{

		.form-control,
		.input-group-addon{
			font-size: $form-sm-font-size;
			height: $form-ig-sm-btn-size;
		}//input-group-addon


		.input-group-addon{
			padding-top: 0;
			padding-bottom: 0;
		}//input-group-addon


		.input-group-prepend,
		.input-group-append{
			height: $form-ig-sm-btn-size;

			.btn{
				// font-size: $form-sm-font-size;
				height: $form-ig-sm-btn-size;
				padding: 0 $form-sm-padding-x;


				&.btn-icon{
					font-size: $form-ig-sm-btn-icon;
					line-height: $form-ig-sm-btn-line-height;
					width: $form-ig-sm-btn-size + 4;
					padding: 0;
				}//btn-icon

			}//btn
	
		}//input-group-prepend, input-group-append

	}//input-group-sm





	// LG
	&.input-group-lg{

		.form-control,
		.input-group-addon{
			font-size: $form-lg-font-size;
			height: $form-ig-lg-btn-size;
		}//input-group-addon
		
		
		.input-group-addon{
			padding-top: 0;
			padding-bottom: 0;
			border-radius: $global-radius;
		}//input-group-addon


		.input-group-prepend,
		.input-group-append{
			height: $form-ig-lg-btn-size;

			.btn{
				font-size: $form-lg-font-size;
				height: $form-ig-lg-btn-size;
				padding: 0 $form-lg-padding-x;
				// border-radius: $global-radius;


				&.btn-icon{
					font-size: $form-ig-lg-btn-icon;
					line-height: $form-ig-lg-btn-line-height;
					width: $form-ig-lg-btn-size + 4;
					padding: 0;
				}//btn-icon
			}//btn
	
		}//input-group-prepend, input-group-append

	}//input-group-lg



	.input-group-prepend{
		.btn.btn-rounded{ 
			border-top-right-radius: 0;
			border-bottom-right-radius: 0;
		}//btn-rounded
	}//input-group-prepend


	.input-group-append{
		.btn.btn-rounded{ 
			border-top-left-radius: 0;
			border-bottom-left-radius: 0;
			}//btn-rounded
	}//input-group-append




	&.input-group-inset{

		.input-group-prepend .input-group-text{
			background: $form-background;
			border-right: none;
		}//input-group-text

		.input-group-append .input-group-text{
			background: $form-background;
			border-left: none;
		}//input-group-text


		.input-group-prepend + .form-control{
			border-left: none;
		}

	}//input-group-inset


}//input-group






// Roundcorner for input groups
.input-group-rounded{
	border-radius: 		$form-rounded-radius;

	.form-control, 
	.input-group-addon, 
	.input-group-btn,
	.btn{
		border-radius: 	$form-rounded-radius;
	}
}//input-group-rounded