/*-------------------------------------------------*/
/*  DROPDOWN
/*-------------------------------------------------*/
.dropdown-toggle{

	.bs-caret{
		display: none !important;
	}//bs-caret

}//dropdown-toggle




.dropdown-menu{
	// padding: 0;
	min-width: $dropdown-min-width;
	border-color: $dropdown-border-color;
	border-radius: $dropdown-border-radius;


	> li a{
		color: $gray-dark;
		font-size: $dropdown-font-size;
		padding: $dropdown-item-padding-y $dropdown-item-padding-x;
		display: inline-block;


		&:hover, &:active, &:focus{
			text-decoration: none;
		}//:hover

	}// > li a

}//dropdown-menu



// BUTTON GROUP
.btn-group{

	.dropdown-toggle{
		padding-right: ($btn-spacer-x+15);
		
		&::after{
			content: 				'';
			border-left: 		$btn-dropdown-arrow / 4 * 3 solid transparent;
			border-right: 	$btn-dropdown-arrow / 4 * 3 solid transparent;
			border-top: 		$btn-dropdown-arrow solid;
			display: 				inline-block;
			height: 				0;
			margin-left: 		15px;
			margin-right: 	-20px;
			vertical-align: middle;
			width: 					0;
		}

	}//dropdown-toggle



	&.open{

		.btn-default 	{ @include btn-dropdown($gray-lighter); }
		.btn-primary 	{ @include btn-dropdown($brand-primary); }
		.btn-success 	{ @include btn-dropdown($brand-success); }
		.btn-info 		{ @include btn-dropdown($brand-info); }
		.btn-warning 	{ @include btn-dropdown($brand-warning); }
		.btn-danger 	{ @include btn-dropdown($brand-danger); }
		.btn-link 		{ @include btn-dropdown(transparent); }

	}//open 

}//btn-group





