// ICON THUMBNAILS
$icon-thumbnail-bg: 						darken($gray-lighter,5%) !default;
$icon-thumbnail-color: 					$gray-dark !default;
$icon-thumbnail-width: 					40px !default;
$icon-thumbnail-height: 				40px !default;
$icon-thumbnail-font-size: 			$icon-thumbnail-height/2 !default;
$icon-thumbnail-font-weight: 		500 !default;
$icon-thumbnail-line-height: 		$icon-thumbnail-height !default;
$icon-thumbnail-border-radius: 	3px !default;