/*---------------------------------------------------------*/
/*	LIST GROUPS
/*---------------------------------------------------------*/
$list-item-bg: 									transparent !default;
$list-item-border: 							1px solid rgba(#000, .125) !default;
$list-item-font-size: 					.9rem !default;
$list-item-line-height: 				1.3 !default;
$list-item-padding-y: 					10px !default;
$list-item-padding-x: 					15px !default;
$list-item-radius: 							$global-radius !default;

// ACTIVE
$list-item-active-bg: 					$brand-info !default;
$list-item-active-border: 			$brand-info !default;
$list-item-active-color: 				#fff !default;

// DISABLED
$list-item-disabled-color: 			$gray-light !default;


$list-item-action-hover-bg: 		#ECF0F1 !default;
$list-item-action-hover-color: 	darken($global-font-color, 10%) !default;


$list-item-contextual-color: 		#fff !default;

// FLUSH
$list-item-flush-bg: 						transparent !default;

$list-item-zebra-odd-bg: 				#ECF0F1 !default;
$list-item-zebra-even-bg: 			transparent !default;




// MEDIA
$media-margin-top: 							30px !default;

$media-heading-color: 					#4d4d4d !default;
$media-heading-font-family: 		$font-primary !default;
$media-heading-font-size: 			1.05rem !default;
$media-heading-font-weight: 		500 !default;
$media-heading-line-height: 		1.3 !default;


