/*---------------------------------------------------------*/
/*  GLOBALS
/*---------------------------------------------------------*/
.absolute-top-left      {position: absolute !important; top:0; right:auto; bottom:auto; left:0;}
.absolute-top-middle    {position: absolute !important; top:0; right:auto; bottom:auto; left:50%; transform:translateX(-50%);}
.absolute-top-right     {position: absolute !important; top:0; right:0; bottom:auto; left:auto;}
.absolute-center-left   {position: absolute !important; top:50%; right:auto; bottom:auto; left:0; transform:translateY(-50%);}
.absolute-center-center {position: absolute !important; top:50%; right:auto; bottom:auto; left:50%; transform:translate(-50%,-50%);}
.absolute-center-right  {position: absolute !important; top:50%; right:0; bottom:auto; left:auto; transform:translateY(-50%);}
.absolute-bottom-left   {position: absolute !important; top:auto; right:auto; bottom:0; left:0;}
.absolute-bottom-middle {position: absolute !important; top:auto; right:auto; bottom:0; left:50%; transform:translateX(-50%);}
.absolute-bottom-right  {position: absolute !important; top:auto; right:0; bottom:0; left:auto;}


.fixed-top-left         {position: fixed !important; top:0; right:auto; bottom:auto; left:0;}
.fixed-top-center       {position: fixed !important; top:0; right:auto; bottom:auto; left:50%; transform:translateX(-50%);}
.fixed-top-right        {position: fixed !important; top:0; right:0; bottom:auto; left:auto;}
.fixed-center-left      {position: fixed !important; top:50%; right:auto; bottom:auto; left:0; transform:translateY(-50%);}
.fixed-center-center    {position: fixed !important; top:50%; right:auto; bottom:auto; left:50%; transform:translate(-50%,-50%);}
.fixed-center-right     {position: fixed !important; top:50%; right:0; bottom:auto; left:auto; transform:translateY(-50%);}
.fixed-bottom-left      {position: fixed !important; top:auto; right:auto; bottom:0; left:0;}
.fixed-bottom-center    {position: fixed !important; top:auto; right:auto; bottom:0; left:50%; transform:translateX(-50%);}
.fixed-bottom-right     {position: fixed !important; top:auto; right:0; bottom:0; left:auto;}


.overflow-hidden		    {overflow: hidden;}
.overflow-auto			    {overflow: auto;}









