html{font-size: $global-font-size;}

body{
	background: $global-bg-color; 
	color: $global-font-color; 
	font-family: $font-primary;
	font-size: inherit;
	font-weight: $global-font-weight;
	line-height: $global-line-height;

	// text-rendering: optimizeLegibility;
	// text-rendering: geometricPrecision;
	// font-smooth: always;

	// font-smoothing: antialiased;
	-moz-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	-webkit-font-smoothing: antialiased;
	-webkit-font-smoothing: subpixel-antialiased;
}



.jumbotron{margin: 0;}

.section{margin: 0;}


// Fixed style for the Affix Bootstrap plugin
.affix{
	top: 0;
	position: fixed;
}


*,
*:focus,
.focus{
	outline: 0 none !important;
}






/*---------------------------------------------------------*/
/*  IMAGES
/*	Fit images to it's container with object-fit
/*---------------------------------------------------------*/
.img-contain,
.img-cover,
.img-scale-down,
.img-center{
	width: 100%;
	height: 100%;
}


// centers all objects horizontally and vertically without a value
.img-center{ 
	object-fit: none; 
	object-position: 50% 50%;
}

// The image keeps its original aspect ratio, but resized 
// so that the longest of either the height or width can fit 
// in the given dimensions.
.img-contain{ object-fit: contain; }

// The image keeps its original aspect ratio and the image 
// area is completely covered.
.img-cover{ object-fit: cover; }

// The smaller of either contain or none.
.img-scale-down{ object-fit: scale-down; }






