/*---------------------------------------------------------*/
/*  FILTERS
/*---------------------------------------------------------*/
.blur-1{ @include filter-blur(1px); }
.blur-2{ @include filter-blur(2px); }
.blur-3{ @include filter-blur(3px); }
.blur-4{ @include filter-blur(4px); }
.blur-5{ @include filter-blur(5px); }


.contrast-1{ @include filter-contrast(150%); }
.contrast-2{ @include filter-contrast(200%); }
.contrast-3{ @include filter-contrast(300%); }
.contrast-4{ @include filter-contrast(400%); }


.brightness-1{ @include filter-brightness(1.4); }
.brightness-2{ @include filter-brightness(1.8); }
.brightness-3{ 
	$brightness: 1.8;
	$contrast: 200%;
	
	-webkit-filter: brightness($brightness) contrast($contrast);
	-moz-filter: 		brightness($brightness) contrast($contrast);
	-o-filter: 			brightness($brightness) contrast($contrast);
	-ms-filter: 		brightness($brightness) contrast($contrast);
	filter: 				brightness($brightness) contrast($contrast);
}
.brightness-4{ 
	$brightness: 1.8;
	$contrast: 300%;
	$saturate: 5;
	
	-webkit-filter: brightness($brightness) contrast($contrast) saturate($saturate);
	-moz-filter: 		brightness($brightness) contrast($contrast) saturate($saturate);
	-o-filter: 			brightness($brightness) contrast($contrast) saturate($saturate);
	-ms-filter: 		brightness($brightness) contrast($contrast) saturate($saturate);
	filter: 				brightness($brightness) contrast($contrast) saturate($saturate);
}



.grayscale-1{ @include filter-grayscale(.7); }
.grayscale-2{ @include filter-grayscale(1); }
.grayscale-3{ 
	$contrast: 150%;

	-webkit-filter: grayscale(1) contrast($contrast);
	-moz-filter: 		grayscale(1) contrast($contrast);
	-o-filter: 			grayscale(1) contrast($contrast);
	-ms-filter: 		grayscale(1) contrast($contrast);
	filter: 				grayscale(1) contrast($contrast);
}
.grayscale-4{ 
	$contrast: 200%;

	-webkit-filter: grayscale(1) contrast($contrast);
	-moz-filter: 		grayscale(1) contrast($contrast);
	-o-filter: 			grayscale(1) contrast($contrast);
	-ms-filter: 		grayscale(1) contrast($contrast);
	filter: 				grayscale(1) contrast($contrast);
}
.grayscale-5{ 
	$contrast: 400%;

	-webkit-filter: grayscale(1) contrast($contrast);
	-moz-filter: 		grayscale(1) contrast($contrast);
	-o-filter: 			grayscale(1) contrast($contrast);
	-ms-filter: 		grayscale(1) contrast($contrast);
	filter: 				grayscale(1) contrast($contrast);
}


.saturate-1{ @include filter-saturate(200%); }
.saturate-2{
	$contrast: 200%;
	
	-webkit-filter: saturate(200%) contrast($contrast);
	-moz-filter: 		saturate(200%) contrast($contrast);
	-o-filter: 			saturate(200%) contrast($contrast);
	-ms-filter: 		saturate(200%) contrast($contrast);
	filter: 				saturate(200%) contrast($contrast);
}
.saturate-3{
	$contrast: 400%;
	
	-webkit-filter: saturate(200%) contrast($contrast);
	-moz-filter: 		saturate(200%) contrast($contrast);
	-o-filter: 			saturate(200%) contrast($contrast);
	-ms-filter: 		saturate(200%) contrast($contrast);
	filter: 				saturate(200%) contrast($contrast);
}



.hue-1{ @include filter-hue-rotate(25deg); }
.hue-2{ @include filter-hue-rotate(50deg); }
.hue-3{ @include filter-hue-rotate(100deg); }
.hue-4{ @include filter-hue-rotate(150deg); }
.hue-5{ @include filter-hue-rotate(200deg); }
.hue-6{ @include filter-hue-rotate(250deg); }
.hue-7{ @include filter-hue-rotate(300deg); }


.sepia-1{ @include filter-sepia(70%); }
.sepia-2{ @include filter-sepia(100%); }
.sepia-3{ 
	$contrast: 150%;
	
	-webkit-filter: sepia(1) contrast($contrast);
	-moz-filter: 		sepia(1) contrast($contrast);
	-o-filter: 			sepia(1) contrast($contrast);
	-ms-filter: 		sepia(1) contrast($contrast);
	filter: 				sepia(1) contrast($contrast);
}
.sepia-4{ 
	$brightness: 150%;
	
	-webkit-filter: sepia(1) brightness($brightness);
	-moz-filter: 		sepia(1) brightness($brightness);
	-o-filter: 			sepia(1) brightness($brightness);
	-ms-filter: 		sepia(1) brightness($brightness);
	filter: 				sepia(1) brightness($brightness);
}
.sepia-5{
	$saturate: 200%;
	
	-webkit-filter: sepia(1) saturate($saturate);
	-moz-filter: 		sepia(1) saturate($saturate);
	-o-filter: 			sepia(1) saturate($saturate);
	-ms-filter: 		sepia(1) saturate($saturate);
	filter: 				sepia(1) saturate($saturate);
}



.invert-1{ @include filter-invert(1); }
.invert-2{
	$contrast: 200%;
	
	-webkit-filter: invert(1) contrast($contrast);
	-moz-filter: 		invert(1) contrast($contrast);
	-o-filter: 			invert(1) contrast($contrast);
	-ms-filter: 		invert(1) contrast($contrast);
	filter: 				invert(1) contrast($contrast);
}





/*---------------------------------------------------------*/
/*  ANIMATION DELAY
/*---------------------------------------------------------*/
@mixin animation-delay($anim: .1s){animation-delay:$anim; -webkit-animation-delay:$anim;}
.animdelay-1  {@include animation-delay(.5s);}
.animdelay-2  {@include animation-delay(.75s);}
.animdelay-3  {@include animation-delay(1s);}
.animdelay-4  {@include animation-delay(1.25s);}
.animdelay-5  {@include animation-delay(1.5s);}
.animdelay-6  {@include animation-delay(1.75s);}
.animdelay-7  {@include animation-delay(2s);}
.animdelay-8  {@include animation-delay(2.25s);}
.animdelay-9  {@include animation-delay(2.5s);}
.animdelay-10 {@include animation-delay(2.75s);}






