/*-------------------------------------------------*/
/* BACK TO TOP
/*-------------------------------------------------*/
.scroll-to-top{
	background: rgba(#000,.5) url('../images/_nitro/arrow-top-light.png') no-repeat center center / 60% auto; 
	width: 50px; 
	height: 50px; 
	display: block; 
	right: 15px; 
	bottom: 15px; 
	position: fixed; 
	border-radius: 1rem; 
	z-index: 100;
}//scroll-to-top





/*---------------------------------------------------------*/
/*  PARALLAX
/*---------------------------------------------------------*/
.jarallax{
	position: relative;
	overflow: hidden;
	z-index: 0;


	.jarallax-img{
		position: absolute;
		object-fit: cover;
		/* support for plugin https://github.com/bfred-it/object-fit-images */
		font-family: 'object-fit: cover;';
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: -1;
	}//jarallax-img

}//jarallax







