/*---------------------------------------------------------*/
/*  ALERTS
/*---------------------------------------------------------*/
.alert{
	background: 		$alert-bg;
  border-color: 	$alert-border-color;
  color: 					$alert-color;
  font-size: 			$alert-font-size;
  font-weight: 		$alert-font-weight;
  line-height: 		$alert-line-height;
  padding: 				$alert-padding-y $alert-padding-x;
  border-radius: 	$global-radius;
  box-shadow: 		$alert-shadow;
  @include clearfix;
  position: relative;


	button.close{
		width: $alert-close-size;
		height: $alert-close-size;
	}//close



  &.alert-dismissable, 
  &.alert-dismissible{
    padding-right: 35px;
  }//alert-dismissible


	&.alert-rounded{
		border-radius: $global-radius*100;
	}//alert-rounded


	// ALERT BLOCK
	.alert-block{
		width: 	calc(100% - #{$alert-image-size});
		float: 	right;

		&:first-child{
			padding-left: 	0;
			float: 					left;
		}//first-child
	}//alert-block


	&.alert-rounded .alert-block{
		padding-right: 10px;

		&:first-child{ padding-left: 10px; }
	}//alert-rounded alert-block





	// IMAGES
	.alert-image{
		width: 						$alert-image-size;
		height: 					$alert-image-size;
		margin: 					(-$alert-padding-y) 0 (-$alert-padding-y) (-$alert-padding-x);
		float: 						left;
		border-radius: 		$global-radius;

		&:first-child{
			border-radius: 	$global-radius 0 0 $global-radius;
		}

		&:last-child{
			margin: 				(-$alert-padding-y) (-$alert-padding-x) (-$alert-padding-y) 0;
			float: 					right;
			border-radius: 	0 $global-radius $global-radius 0;
		}
	}//alert-image




	&.alert-rounded .alert-image{
		border-radius: 		$global-radius*100;
		margin: 					(-$alert-padding-y) 0 (-$alert-padding-y) (-$alert-padding-x -1);

		&:last-child{
			margin: 				(-$alert-padding-y) (-$alert-padding-x - 1) (-$alert-padding-y) 0;
		}
	}//alert-rounded alert-image

}//alert









// CONTEXTUAL
.alert-success{
	background: 		$brand-success-lighter;
	border-color: 	$brand-success-lighter;
	color: 					darken($brand-success-darker, 10%);

	.close{
		color: $brand-success-darker;
	}
}//alert-success




.alert-info{
	background: 		$brand-info-lighter;
	border-color: 	$brand-info-lighter;
	color: 					darken($brand-info-darker, 30%);

	.close{
		color: $brand-info-darker;
	}
}//alert-success




.alert-warning{
	background: 		$brand-warning-lighter;
	border-color: 	$brand-warning-lighter;
	color: 					darken($brand-warning-darker, 10%);

	.close{
		color: $brand-warning-darker;
	}
}//alert-success




.alert-danger{
	background: 		lighten($brand-danger-lighter, 10%);
	border-color: 	lighten($brand-danger-lighter, 10%);
	color: 					darken($brand-danger-darker, 15%);

	.close{
		color: $brand-danger-darker;
	}
}//alert-success
















/*-------------------------------------------------*/
/*	LABELS & BADGES
/*-------------------------------------------------*/
.label{
	background: 			transparent;
	border: 					solid 1px $gray-light;
	color: 						$gray;
	font-size: 				$label-font-size;
	font-weight: 			$label-font-weight;
	line-height: 			1.2;
	letter-spacing: 	$label-letter-spacing;
	padding: 					$label-padding-y $label-padding-x;
	display: 					inline-block;
	white-space: 			nowrap;
	border-radius: 		$global-radius;

	&.label-default{ 	
		background: $gray-light; 
		border-color: $gray-light;
		color: #fff; 
	}

	&.label-primary{ 	
		background: $brand-primary; 
		border-color: $brand-primary; 
		color: #fff; 
	}

	&.label-success{ 	
		background: $brand-success; 
		border-color: $brand-success; 
		color: #fff; 
	}

	&.label-info{ 		
		background: $brand-info; 
		border-color: $brand-info; 
		color: #fff; 
	}

	&.label-warning{ 	
		background: $brand-warning; 
		border-color: $brand-warning; 
		color: #fff; 
	}

	&.label-danger{ 	
		background: $brand-danger; 
		border-color: $brand-danger; 
		color: #fff; 
	}
}//label




.badge{
	background: 			$gray;
	font-size: 				$badge-font-size;
	font-weight: 			$badge-font-weight;
	letter-spacing: 	$label-letter-spacing;
	line-height: 			$badge-alert-min-size;
	min-width: 				$badge-alert-min-size;
	min-height: 			$badge-alert-min-size;
	padding: 					$badge-padding-y $badge-padding-x;
	border-radius: 		$global-radius * 100;

	&.badge-default {background: $gray;}
	&.badge-primary {background: $brand-primary;}
	&.badge-success {background: $brand-success;}
	&.badge-info 		{background: $brand-info;}
	&.badge-warning {background: $brand-warning;}
	&.badge-danger 	{background: $brand-danger;}
}//badge




// BADGE ALERTS
[data-badge]{
	position: relative;

	&::after{
		content: 				attr(data-badge);
		background: 		$brand-danger;
		color: 					$badge-color;
		font-size: 			$badge-font-size;
		font-weight: 		$badge-font-weight;
		line-height: 		$badge-alert-min-size;
		min-width: 			$badge-alert-min-size;
		min-height: 		$badge-alert-min-size;
		padding: 				$badge-padding-y $badge-padding-x;
		right: 					-$badge-alert-min-size / 2.5;
		top: 						-$badge-alert-min-size / 2.5;
		position: 			absolute;
		border-radius: 	$global-radius * 100;
	}


	&.animated{
		&::after{
			-webkit-animation: burst 2s infinite linear;
			animation: burst 2s infinite linear
		}
	}

}//[data-badge]





@-webkit-keyframes burst{
	0%   { opacity: 0; }
	80%  { opacity: 1; }
	100% { opacity: 1; }
}

@-moz-keyframes burst{
	0%   { opacity: 0; }
	80%  { opacity: 1; }
	100% { opacity: 1; }
}

@keyframes burst{
	0%   { opacity: 0; }
	80%  { opacity: 1; }
	100% { opacity: 1; }
}








/*-------------------------------------------------*/
/*	PROGRESS BARS
/*-------------------------------------------------*/
.progress{
	background-color: $progress-background;
	height: $progress-height;
	margin-bottom: $progress-margin;
	border-radius: $progress-rounded;
	box-shadow: $progress-box-shadow;
	overflow: visible;


	.progress-bar{
		background-color: $progress-bar-background;
		position: relative;
		border-radius: $progress-rounded;
		box-shadow: $progress-box-shadow;


		> span:not(.sr-only){
			background: #fff;
			border: solid 3px #BDC3C8;
			color: #666;
			font-size: 11.5px;
			font-weight: 700;
			line-height: 34px;
			text-align: center;
			width: 36px;
			height: 36px;
			right: 0;
			top: -15px;
			position: absolute;
			white-space: nowrap;
			border-radius: 300px;
			box-sizing: content-box;
		}// > span:not(.sr-only)


		&.progress-bar-primary{
			background-color: $brand-primary;

			> span:not(.sr-only){ border-color: $brand-primary; }
		}

		&.progress-bar-info{
			background-color: $brand-info;

			> span:not(.sr-only){ border-color: $brand-info; }
		}

		&.progress-bar-success{
			background-color: $brand-success;

			> span:not(.sr-only){ border-color: $brand-success; }
		}

		&.progress-bar-warning{
			background-color: $brand-warning;

			> span:not(.sr-only){ border-color: $brand-warning; }
		}

		&.progress-bar-danger{
			background-color: $brand-danger;

			> span:not(.sr-only){ border-color: $brand-danger; }
		}

	}//progress-bar

}//progress








/*-------------------------------------------------*/
/*	CIRCULAR PROGRESS BARS
/*-------------------------------------------------*/
.progress-circular{
	// color: #333;
	line-height: 1.2;
	text-align: center;
	width: 100%;
	height: auto;
	position: relative;
	display: inline-block;


	svg{
		width: 100%;
		height: 100%;
		transform: rotate(-90deg);
	}

	.meter,
	.value{
		stroke-width: 6px;
		fill: none;
	}

	.meter{
		stroke: #ECF0F1;
	}

	.value{
		stroke: #BDC3C8;
		stroke-dasharray: 339.292;
    // stroke-dashoffset: 135.717;
	}

	> .progress-circular-value{
		font-size: .9rem;
		font-weight: 600;
		width: 100%;
		display: block;
		left: 0;
		top: 50%;
		position: absolute;
		transform: translateY(-50%);
	}//progress-circular-value



	&.progress-circular-primary{
		.value{ stroke: $brand-primary; }
	}//progress-circular-primary

	&.progress-circular-info{
		.value{ stroke: $brand-info; }
	}//progress-circular-primary

	&.progress-circular-success{
		.value{ stroke: $brand-success; }
	}//progress-circular-primary

	&.progress-circular-warning{
		.value{ stroke: $brand-warning; }
	}//progress-circular-primary

	&.progress-circular-danger{
		.value{ stroke: $brand-danger; }
	}//progress-circular-primary


}//.progress-circular