/*---------------------------------------------------------*/
/*	KEN BURNS IN
/*---------------------------------------------------------*/
[data-animation=kenburns-in]{
	.owl-item{ perspective: 400px; }
	.owl-item.kenburns > div{ animation: owl-kenburns-in linear; }
}//[data-animation="kenburns-in"]

@keyframes owl-kenburns-in {
	0% { transform: translateZ(100px); }
	100% { transform: translateZ(0); }
}










/*---------------------------------------------------------*/
/*	KEN BURNS OUT
/*---------------------------------------------------------*/
[data-animation=kenburns-out]{
	.owl-item{ perspective: 400px; }
	.owl-item.kenburns > div{ animation: owl-kenburns-out linear; }
}//[data-animation="kenburns-out"]

@keyframes owl-kenburns-out {
	0% { transform: translateZ(0); }
	100% { transform: translateZ(100px); }
}










/*---------------------------------------------------------*/
/*	FADE
/*---------------------------------------------------------*/
[data-animation=fade]{

	.fadeIn{ animation-name: fadeIn; }
	.fadeOut{ animation-name: fadeOut; }

}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes fadeOut {
  from { opacity: 1; }
  to { opacity: 0; }
}










/*---------------------------------------------------------*/
/*	ZOOM
/*---------------------------------------------------------*/
[data-animation=zoom]{
	.owl-stage{ perspective: 400px; }
	.owl-zoom-in{ animation-name: owl-fadeIn; }
}

@keyframes owl-fadeIn {
	from { transform: scale(3); opacity: 0; }
  to { transform: scale(1); opacity: 1; }
}

