/*-------------------------------------------------*/
/*	SCROLL DOWN MOUSE
/*-------------------------------------------------*/
.scroll-down-mouse{
	border: solid 2px rgba(#fff,.8); 
	width: 26px; 
	height: 42px; 
	display: inline-block;
	position: relative; 
	border-radius: 100px; 
	opacity: .8; 
	z-index: 100;

	&:after{
		content: ''; 
		background: #fff; 
		width: 4px; 
		height: 4px; 
		margin-left: -2px; 
		left: 50%; 
		top: 5px; 
		position: absolute; 
		border-radius: 100px;

		-webkit-animation: scroll-down-mouse-move-down 2s ease-in-out infinite;
		-moz-animation:    scroll-down-mouse-move-down 2s ease-in-out infinite;
		animation:         scroll-down-mouse-move-down 2s ease-in-out infinite;
	}
}//scroll-down-mouse


.scroll-down-mouse.light{
	border: solid 2px rgba(#fff,.8); 
	
	&:after{
		background: #fff;
	}
}//scroll-down-mouse


.scroll-down-mouse.dark{
	border: solid 2px rgba(#000,.8); 

	&:after{
		background: #000;
	}
}//scroll-down-mouse


@-webkit-keyframes scroll-down-mouse-move-down {
	0%   { height:4px; opacity: 1; }
	35%  { height:16px; top:5px; }
	65%  { height:4px; top:16px; opacity: 1; }
	100% { top:18px; opacity: 0; }
}

@-moz-keyframes scroll-down-mouse-move-down {
	0%   { height:4px; opacity: 1; }
	35%  { height:16px; top:5px; }
	65%  { height:4px; top:16px; opacity: 1; }
	100% { top:18px; opacity: 0; }
}

@keyframes scroll-down-mouse-move-down {
	0%   { height:4px; opacity: 1; }
	35%  { height:16px; top:5px; }
	65%  { height:4px; top:16px; opacity: 1; }
	100% { top:18px; opacity: 0; }
}














/*-------------------------------------------------*/
/*	ICON THUMBNAILS
/*-------------------------------------------------*/
.icon-thumbnail{
	background: $icon-thumbnail-bg;
	color: $icon-thumbnail-color;
	font-family: $font-primary;
	font-size: $icon-thumbnail-font-size;
	font-weight: $icon-thumbnail-font-weight;
	line-height: $icon-thumbnail-line-height;
	text-align: center;
	width: $icon-thumbnail-width;
	height: $icon-thumbnail-height;
	display: inline-block;
	vertical-align: middle;
	border-radius: $icon-thumbnail-border-radius;
}//icon-thumbnail