/*---------------------------------------------------------*/
/*	TYPOGRAPHY
/*---------------------------------------------------------*/
// PRIMARY & SECONDARY FONTS
$font-primary: 						-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif !default;


$font-h1-font-size: 			2.5rem !default;
$font-h1-font-weight: 		300 !default;

$font-h2-font-size: 			2rem !default;
$font-h2-font-weight: 		300 !default;

$font-h3-font-size: 			1.5rem !default;
$font-h3-font-weight: 		300 !default;

$font-h4-font-size: 			1.2rem !default;
$font-h4-font-weight: 		400 !default;

$font-h5-font-size: 			1rem !default;
$font-h5-font-weight: 		500 !default;

$font-h6-font-size: 			.85rem !default;
$font-h6-font-weight: 		600 !default;

$font-bold: 							600 !default;

$font-small-size: 				75% !default;

$lead-fz: 								1.3rem !default;


$supsub-font-size: 				70% !default;

$text-muted-color: 				lighten($global-font-color, 30%) !default;
$text-muted-font-weight: 	inherit !default;

$p-spacer: 								20px !default;




// DISPLAY HEADINGS
$display-1-fz: 		6rem !default;
$display-2-fz: 		5rem !default;
$display-3-fz: 		4.3rem !default;
$display-4-fz: 		3.3rem !default;

$display-fw: 			300 !default;
$display-lh: 			1.1 !default;





// LISTS
$list-line-height: 		1.7 !default;

$list-ol-spacer-x: 		15px !default;

$list-dot-spacer-x: 	13px !default;





// DESCRIPTION LISTS
$dd-margin: 	20px !default;





