/*---------------------------------------------------------*/
/*	VALIDATION STATES
/*---------------------------------------------------------*/
.has-success{
	
		.form-control{
			border-color: $brand-success;
			color: $brand-success-dark;
		}//form-control
	
		.control-label{
			color: $brand-success-dark;
		}//control-label
	
		.help, .help-block{
			color: $brand-success;
		}//help, help-block
	
	}//has-success
	
	
	
	
	.has-warning{
	
		.form-control{
			border-color: $brand-warning;
			color: $brand-warning-dark;
		}//form-control
	
		.control-label{
			color: $brand-warning-dark;
		}//control-label
	
		.help, .help-block{
			color: $brand-warning;
		}//help, help-block
	
	}//has-warning
	
	
	
	
	.has-error{
	
		.form-control{
			border-color: $brand-danger;
			color: $brand-danger-dark;
		}//form-control
	
		.control-label{
			color: $brand-danger-dark;
		}//control-label
	
		.help, .help-block{
			color: $brand-danger;
		}//help, help-block
	
	}//has-error






/*---------------------------------------------------------*/
/*	VALIDATION (Parsley)
/*---------------------------------------------------------*/
// ERROR FIELD
.parsley-error,
.form-control.parsley-error{
	background: inherit;
	border-color: $brand-danger;
}


// VALID FIELD
.parsley-error,
.form-control.parsley-success{
	background: inherit;
	color: $brand-success;
	border-color: $brand-success;
}


.parsley-errors-list{
	padding-left: 0;
	margin: 0;

	&.filled{}

	.parsley-custom-error-message{
		color: $brand-danger;
		font-size: .7rem;
		line-height: 1.1;
		font-weight: 400;
		padding-top: 7px;
		list-style: none;
	}

}//parsley-errors-list