/*-------------------------------------------------*/
/*  BUTTON ICONS
/*-------------------------------------------------*/
.btn-icon{
	font-size: 				$btn-icon-font-size;
	line-height: 			$btn-icon-line-height; // -2px from the border width
	width: 						$btn-icon-width;
	height: 					$btn-icon-height;
	padding: 					$btn-icon-padding;


	$btn-lg-size: 1.4;


	svg.icon{
		width: 20px;
		height: 20px;
		margin-top: 7px;
	}//svg.icon



	&.btn-lg{
		font-size: 		$btn-icon-font-size * $btn-lg-size;
		line-height: 	$btn-icon-line-height * $btn-lg-size;
		width: 				$btn-icon-width * $btn-lg-size;
		height: 			$btn-icon-height * $btn-lg-size;
	}//btn-lg


	$btn-sm-size: 1.3;

	&.btn-sm{
		font-size: 		$btn-icon-font-size / $btn-sm-size;
		line-height: 	$btn-icon-line-height / $btn-sm-size;
		width: 				$btn-icon-width / $btn-sm-size;
		height: 			$btn-icon-height / $btn-sm-size;
	}//btn-sm


	$btn-xs-size: 1.7;

	&.btn-xs{
		font-size: 		$btn-icon-font-size / $btn-xs-size;
		line-height: 	$btn-icon-line-height / $btn-xs-size;
		width: 				$btn-icon-width / $btn-xs-size;
		height: 			$btn-icon-height / $btn-xs-size;
	}//btn-xs

}//btn-icon





