/*---------------------------------------------------------*/
/*	RANGE
/*---------------------------------------------------------*/
.form-control-range{
  -webkit-appearance: none;
  width: 100%;
  margin: ($range-track-size * 2) 0;

	&:focus{
		outline: none;
	}//:focus


	&::-webkit-slider-runnable-track{
		background: $progress-bar-background;
		border: none;
		width: 100%;
		height: $range-track-size;
		border-radius: $range-track-radius;
		cursor: pointer;
	}//-webkit-slider-runnable-track


	&::-webkit-slider-thumb{
		// background: $progress-background;
		background: #fff;
		border: solid $range-thumb-border $progress-bar-background;
		height: $range-thumb-size;
		width: $range-thumb-size;
		margin-top: -(($range-track-size * 2) - $range-thumb-border);
		// margin-top: -6.5px;
		border-radius: $range-track-radius;
		-webkit-appearance: none;
		cursor: pointer;
	}//-webkit-slider-thumb


	&:focus::-webkit-slider-runnable-track{
		background: darken($progress-bar-background, 5%);
	}//:focus::-webkit-slider-runnable-track


	&:focus::-webkit-slider-thumb{
		border-color: darken($progress-bar-background, 5%);
	}//:focus::-webkit-slider-thumb


	&::-moz-range-track{
		background: $progress-bar-background;
		width: 100%;
		height: $range-track-size;
		cursor: pointer;
		border-radius: $range-track-radius;
		border: 0px solid #010101;
	}//::-moz-range-track


	&::-moz-range-thumb{
		background: $progress-background;
		border: solid $range-thumb-border $progress-bar-background;
		height: $range-thumb-size;
		width: $range-thumb-size;
		border-radius: $range-track-radius;
		cursor: pointer;
	}//::-moz-range-thumb


	&::-ms-track{
		width: 100%;
		height: 3.9px;
		cursor: pointer;
		background: transparent;
		border-color: transparent;
		color: transparent;
	}//::-ms-track


	&::-ms-fill-lower{
		background: #2a6495;
		border: 0px solid #010101;
		border-radius: 0px;
		box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
	}//::-ms-fill-lower


	&::-ms-fill-upper{
		background: #3071a9;
		border: 0px solid #010101;
		border-radius: 0px;
		box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
	}//::-ms-fill-upper


	&::-ms-thumb{
		box-shadow: 0px 0px 0px #000000, 0px 0px 0px #0d0d0d;
		border: 0px solid rgba(0, 0, 0, 0);
		height: 17px;
		width: 17px;
		border-radius: 50px;
		background: #ffffff;
		cursor: pointer;
		height: 3.9px;
	}//::-ms-thumb


	&:focus::-ms-fill-lower{
		background: #3071a9;
	}//:focus::-ms-fill-lower


	&:focus::-ms-fill-upper{
		background: #367ebd;
	}//:focus::-ms-fill-upper

}//form-control-range