/* Plugins
--------------------------------------------------
Datepicker
https://github.com/eternicode/bootstrap-datepicker
*/
.datepicker{
  padding: 16px 25px;
  border-radius: 2px;
  font-size: 12px;

	&::after{
		border-bottom-color: #fafafa;
	}//::after


	thead{

		tr{

			.datepicker-switch{
				color: #6f7b8a;
				font-size: 13px;
			}//datepicker-switch

			.next, .prev{
				color: #6d5cae;
				content: '';
				font-size: 0px;

				&::before{
					color: #6d5cae;
					font-family: 'FontAwesome';
					font-size: 10px;
				}//::before
			}//next, prev
			
			.prev::before{
				content: "\f053";
			}//prev::before

			.next::before{
				content: "\f054";
			}//next::before

			.dow{
				color: $brand-info;
				text-transform: uppercase;
				font-size: 11px;
			}//dow

			th{
				width: 31px;
				height: 29px;
			}//th

		}//tr

	}//thead


	tbody tr .odd {
		color: #d0d3d8;
	}


	table{
		tr{

			td{
				width: 31px;
				height: 29px;

				&.old, &.new{
					color: #e6e6e6;
				}

				&.day:hover {
					background: #f0f0f0;
				}

				&.active {
					background-color: $brand-info !important;
				}

				&.active,
				&.active:hover,
				&.active.disabled,
				&.active.disabled:hover{
					background-image: none;
					text-shadow: none;
					font-weight: 600;
				}

				&.today,
				&.today:hover,
				&.today.disabled,
				&.today.disabled:hover {
					background-color: lighten($brand-info,20%);
					background-image: none;
					color: #fff;
				}

				span{
					border-radius: 4px;
					width: 42px;
					height: 42px;
					line-height: 42px;
				}

				span.active {
					background-color: #6d5cae !important;
				}

				span.active,
				span.active:hover,
				span.active.disabled,
				span.active.disabled:hover {
					background-image: none;
					border: none;
					text-shadow: none;
				}

			}//td

		}//tr
	}//table

	&.dropdown-menu{
		border-color: #e6e6e6;
		color: #626262;
		box-shadow: 0px 0px 10px rgba(#000, .2);
	}//dropdown-menu

	&.datepicker-dropdown.datepicker-orient-bottom:before{
		border-color: transparent;
	}

}//datepicker


.datepicker-inline{
	width: auto;
	padding: 0;
}

.input-daterange .input-group-addon{
  padding: 0;
}