/*---------------------------------------------------------*/
/*  MODAL
/*---------------------------------------------------------*/
.modal-dialog{

	.card{
		background: #fff;
	}//card

}//modal-dialog




/*---------------------------------------------------------*/
/*  SCALE MODAL
/*---------------------------------------------------------*/
.modal.scale{
	transform: scale(.9) translate(0);
	transform-origin: 50% 50%; 
	transition: .4s; 

	.modal-dialog{
		transform: translate(0,25%);
		margin-top: 0;
		margin-bottom: 0;
	}//modal-dialog


	&.modal-showing{
		transform: scale(1.05) translate(0);
	}


	&.in{
		transform: scale(1) translate(0);
	}//in

}//.modal.scale




// MODAL BACKDROP
// This is the modal overlay...
.modal-backdrop{
	opacity: 0;
	transition: .3s;

	&.in{
		transition: .3s;
	}//in

}//modal-backdrop






